import React, { memo } from 'react';
import PropTypes from 'prop-types';
import moment from 'moment';
import _isArray from 'lodash/isArray';
import _first from 'lodash/first';
import _last from 'lodash/last';
import _get from 'lodash/get';
import _map from 'lodash/map';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import { compose, withState } from 'recompose';
import withRequest from '../../utils/with-request';
import SpacesService from '../../services/spaces';
import SpaceBookingsService from '../../services/space-bookings';
import Header from '../../components/Header';
import Loading from '../../components/Loading';
import AlertBox from '../../components/AlertBox';
import AppCalendar from '../../components/AppCalendar';
import SpaceBookingModal from './SpaceBookingModal';
import styles from './styles';

const DEFAULT_VIEW = 'week';

const SpacesCalendar = memo(({
  classes, alert, setAlert, bookingsFetch, match, setRangeDates,
  bookingsData, bookingsError, bookingsLoading, spaceData,
  spaceError, spaceLoading, calendarView, setCalendarView
}) => {
  const { showAlert, alertMsg, alertType } = alert;
  const onClearAlert = () => setAlert({ alertMsg: '', alertType: '', showAlert: false });
  const setAlertError = (msg) => setAlert({ alertMsg: msg, alertType: 'error', showAlert: true });
  const setAlertSuccess = (msg) => setAlert({ alertMsg: msg, alertType: 'success', showAlert: true });
  let alertBox = null;
  if (showAlert) {
    alertBox = <AlertBox message={alertMsg} type={alertType} onClose={onClearAlert} />;
  }
  const refreshCalendar = async (range) => {
    let variables;
    const spaceId = match.params.id;
    try {
      if (_isArray(range)) {
        variables = { spaceId, from: _first(range), to: _last(range) };
      } else {
        variables = { spaceId, from: range.start, to: range.end };
      }
      setRangeDates({ from: variables.from, to: variables.to });
      await bookingsFetch(true, '', null, { variables });
    } catch (err) {
      setAlertError(_get(err, 'error') || 'Erro ao atualizar calendrio');
    }
  };
  const onCreateBooking = async (err) => {
    try {
      if (err) {
        const errors = _get(err, 'errors.periods', []).join(', ');
        setAlertError(errors || 'Ocorreu um erro ao reservar espao');
      } else {
        setAlertSuccess('Reservado com sucesso');
        await bookingsFetch(true);
      }
    } catch {
      setAlertError('Erro ao atualizar calendrio');
    }
  };
  const onCancelBooking = async (err) => {
    try {
      if (err) {
        const errors = _get(err, 'errors.booking_date', []).join(', ');
        setAlertError(errors || 'Ocorreu um erro ao cancelar esta reserva');
      } else {
        setAlertSuccess('Reserva cancelada com sucesso');
        await bookingsFetch(true);
      }
    } catch {
      setAlertError('Erro ao atualizar calendrio');
    }
  };
  const space = _get(spaceData, 'space', {});
  const spaceBookings = _get(bookingsData, 'space_bookings', []);
  const error = spaceError || bookingsError;
  const bookings = _map(spaceBookings, b => ({
    id: b.id,
    title: b.title || 'No disponvel',
    bookingDate: moment(b.booking_date).toDate(),
    from: moment(b.booking_date)
      .hour(_first(b.from.split(':')))
      .minute(_last(b.from.split(':')))
      .toDate(),
    to: moment(b.booking_date)
      .hour(_first(b.to.split(':')))
      .minute(_last(b.to.split(':')))
      .add(1, 'minute')
      .toDate(),
    busy: !b.title
  }));
  return (
    <div className={classes.root}>
      <Header alertBox={alertBox} error={error} />
      <section>
        {
          !spaceLoading &&
            <div className={classes.container}>
              <div className={classes.headerView}>
                <aside>
                  <Typography variant="h5">
                    <b>{space.name}</b>
                  </Typography>
                </aside>
                <aside className={classes.newBooking}>
                  <SpaceBookingModal
                    space={space}
                    onRequestBooking={onCreateBooking}
                  />
                </aside>
              </div>
            </div>
        }
        <div className={classes.container}>
          {
            spaceLoading ?
              <Loading />
              :
              <AppCalendar
                data={bookings}
                onView={setCalendarView}
                onRangeChange={refreshCalendar}
                view={calendarView}
                loading={bookingsLoading}
                eventComponent={(bookingData) => (
                  <SpaceBookingModal
                    readOnly
                    space={space}
                    isMonthView={calendarView === 'month'}
                    onRequestBooking={onCancelBooking}
                    selectedBookingData={bookingData}
                    openModalButton={<React.Fragment>{bookingData.title}</React.Fragment>}
                  />
                )}
              />
          }
        </div>
      </section>
    </div>
  );
});

SpacesCalendar.propTypes = {
  classes: PropTypes.object,
  match: PropTypes.object,
  alert: PropTypes.object,
  setAlert: PropTypes.func,
  spaceLoading: PropTypes.bool,
  spaceError: PropTypes.object,
  spaceData: PropTypes.object,
  setRangeDates: PropTypes.func,
  bookingsLoading: PropTypes.bool,
  bookingsError: PropTypes.object,
  bookingsData: PropTypes.object,
  bookingsFetch: PropTypes.func,
  calendarView: PropTypes.string,
  setCalendarView: PropTypes.func
};

const withSpace = withRequest(SpacesService.getSpace, {
  name: 'space',
  options: ({ match }) => ({ variables: match.params.id })
});

const withSpaceBookings = withRequest(SpaceBookingsService.getBookings, {
  name: 'bookings',
  options: ({ match: { params: { id } }, rangeDates: { from, to } }) => ({
    variables: { spaceId: id, from, to }
  })
});

export default compose(
  withStyles(styles, { withTheme: true }),
  withState('rangeDates', 'setRangeDates', {
    from: moment().startOf(DEFAULT_VIEW).toDate(),
    to: moment().endOf(DEFAULT_VIEW).toDate()
  }),
  withState('calendarView', 'setCalendarView', DEFAULT_VIEW),
  withState('alert', 'setAlert', {
    alertMsg: '',
    alertType: '',
    showAlert: false
  }),
  withSpace,
  withSpaceBookings
)(SpacesCalendar);
