import moment from 'moment';
import Api from '../utils/api';

const SpaceCalendars = {
  getCalendar({ spaceId, bookingDate, readOnly }) {
    if (readOnly) {
      return new Promise(resolve => resolve({}));
    }
    return Api.get(`/spaces/${spaceId}/space_calendars?booking_date=${moment(bookingDate).toISOString()}`);
  }
};

export default SpaceCalendars;
