import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import _get from 'lodash/get';
import _sortBy from 'lodash/sortBy';
import { Link } from 'react-router-dom';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';
import styles from './styles';
import config from '../../config';
import AuthService from '../../services/auth';
import EnterpriseUserService from '../../services/enterprise-user';
import Session from '../../utils/session';
import Store from '../../utils/store';
import AlertBox from '../../components/AlertBox';

const LOGGED_PATH = '/visitantes';

class Signin extends PureComponent {
  state = {
    username: '',
    password: '',
    errorMsg: '',
    submit: false
  }

  componentDidMount() {
    const { history } = this.props;
    try {
      if (Session.isLoggedIn()) {
        history.push(LOGGED_PATH);
      }
    } catch {
      Session.clear();
    }
  }

  onSubmit = (e) => {
    e.preventDefault();
    const { username, password } = this.state;
    const { history } = this.props;
    this.setState({ submit: true }, async () => {
      try {
        if (username && password) {
          const { token } = await AuthService.postSignin(username, password);
          Session.setUserToken(token);
          const enterpriseUserResponse = await EnterpriseUserService.getEnterpriseUser();
          const enterpriseUser = _get(enterpriseUserResponse, 'enterprise_user');
          Session.setUserData({
            ...enterpriseUser,
            buildings: _sortBy(_get(enterpriseUser, 'buildings'), ['name'])
          });
          setTimeout(() => {
            this.setState({ submit: false });
            const redirectUrl = Store.get('redirectUrl');
            Store.delete('redirectUrl');
            history.push(redirectUrl || LOGGED_PATH);
          }, 50);
          return true;
        }
        throw new Error('Blank fields');
      } catch {
        this.setState({ submit: false, errorMsg: 'Credencial invlida, tente novamente.' });
        return true;
      }
    });
  }

  render() {
    const { username, password, submit, errorMsg } = this.state;
    return (
      <div style={styles.container}>
        <div style={styles.content}>
          {errorMsg && <AlertBox message={errorMsg} type="warning" onClose={() => this.setState({ errorMsg: '' })} />}
          <img src={require('../../assets/images/logo.png')} width={150} alt={config.appName} style={styles.logo} />
          <Typography component="h1" variant="h5">
            Portal da empresa
          </Typography>
          <form onSubmit={this.onSubmit} style={styles.form}>
            <TextField
              variant="outlined"
              margin="normal"
              fullWidth
              label="Email"
              type="text"
              disabled={submit}
              value={username}
              InputLabelProps={{ shrink: true }}
              onChange={e => this.setState({ username: e.target.value.toLowerCase() })}
            />
            <TextField
              variant="outlined"
              margin="normal"
              fullWidth
              label="Senha"
              type="password"
              disabled={submit}
              value={password}
              InputLabelProps={{ shrink: true }}
              onChange={e => this.setState({ password: e.target.value })}
            />
            <Button
              disabled={submit}
              fullWidth
              variant="contained"
              color="primary"
              size="large"
              style={styles.button}
              type="submit"
            >
              {submit ? 'Carregando...' : 'Entrar'}
            </Button>
          </form>
          <p>
            <Link to="/recuperar-senha">Esqueci minha senha</Link>
          </p>
        </div>
      </div>
    );
  }
}

Signin.propTypes = {
  history: PropTypes.object
};

export default Signin;
