import React, { memo } from 'react';
import PropTypes from 'prop-types';
import { compose } from 'recompose';
import { withRouter } from 'react-router';
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';
import _get from 'lodash/get';
import styles from './styles';

const SpacesCard = memo(({ classes, space, history }) => {
  const isDisabled = _get(space, 'in_maintenance');
  const onCardClick = () => {
    if (!isDisabled) {
      history.push(`/espacos/${space.id}`);
    }
  };
  const cardClass = isDisabled ? classes.cardDisabled : classes.card;
  return (
    <Grid item xs={12} sm={6} md={4}>
      <Card className={cardClass} onClick={onCardClick}>
        <CardActionArea>
          <CardMedia
            className={classes.media}
            image={space.photo_url}
            title={space.name}
          />
          <CardContent>
            <Typography variant="h6">
              <small>
                {space.name}
                &nbsp;
              </small>
              {
                isDisabled &&
                  <small className={classes.danger}>
                    (Manuteno)
                  </small>
              }
            </Typography>
          </CardContent>
        </CardActionArea>
      </Card>
    </Grid>
  );
});

SpacesCard.propTypes = {
  classes: PropTypes.object,
  space: PropTypes.object,
  history: PropTypes.object
};

export default compose(
  withStyles(styles, { withTheme: true }),
  withRouter
)(SpacesCard);
