import React, { memo } from 'react';
import { compose, withState, withHandlers } from 'recompose';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import DialogTitle from '@material-ui/core/DialogTitle';
import CloseIcon from '@material-ui/icons/Close';
import IconButton from '@material-ui/core/IconButton';
import FormControl from '@material-ui/core/FormControl';
import TextField from '@material-ui/core/TextField';
import NativeSelect from '@material-ui/core/NativeSelect';
import InputLabel from '@material-ui/core/InputLabel';
import MomentUtils from '@date-io/moment';
import moment from 'moment';
import { MuiPickersUtilsProvider, DatePicker, TimePicker } from '@material-ui/pickers';
import FormHelperText from '@material-ui/core/FormHelperText';
import Grid from '@material-ui/core/Grid';
import _get from 'lodash/get';
import _clone from 'lodash/clone';
import _mapValues from 'lodash/mapValues';
import PropTypes from 'prop-types';
import VisitorsService from '../../../services/visitors';
import withRequest from '../../../utils/with-request';
import Tracker from '../../../utils/tracker';
import Session from '../../../utils/session';
import styles from './styles';
import config from '../../../config';

const userData = Session.getUserData();

const getInitialMemberData = () => {
  return {
    visitorData: _clone([{ name: '', email: '' }]),
    visitorCompany: '',
    visitTime: moment(new Date()).add(1, 'hour').minute(0).toDate(),
    visitDate: new Date(),
    hostMemberId: _get(userData, 'member.id', ''),
    buildingId: _get(userData, 'buildings[0].id', '')
  };
};

const VisitorModal = memo(({
  classes, open, setOpen, loading, setMemberState,
  members, memberData, buildings, submitFetch,
  submitLoading, submitError
}) => {
  const onAddVisitor = () => {
    const { visitorData } = memberData;
    visitorData.splice(1, 0, { name: '', email: '' });
    setMemberState({ visitorData });
  };

  const onRemoveVisitor = (index) => {
    const { visitorData } = memberData;
    visitorData.splice(index, 1);
    setMemberState({ visitorData });
  };

  const onChangeVisitorName = (e, index) => {
    const { visitorData } = memberData;
    visitorData[index].name = e.target.value;
    setMemberState({ visitorData });
  };

  const onChangeVisitorEmail = (e, index) => {
    const { visitorData } = memberData;
    visitorData[index].email = e.target.value;
    setMemberState({ visitorData });
  };

  const handleOpen = () => {
    Tracker.hit('VisitorModal - Viewed');
    setMemberState(getInitialMemberData());
    setOpen(true);
  };

  const handleClose = () => {
    Tracker.hit('VisitorModal - Closed');
    setMemberState(getInitialMemberData());
    setOpen(false);
  };
  const errors = _mapValues(_get(submitError, 'errors', {}), (err) => err[0]);
  const {
    visitorCompany, visitTime, visitDate,
    hostMemberId, visitorData, buildingId
  } = memberData;
  const isDisabled = loading || submitLoading;
  return (
    <div>
      <Button
        onClick={handleOpen}
        size="large"
        variant="contained"
        color="primary"
      >
        Nova visita
      </Button>
      <Dialog open={open} maxWidth="md" onClose={handleClose} keepMounted>
        <DialogTitle>
          Novo visitante
          <IconButton className={classes.closeButton} onClick={handleClose}>
            <CloseIcon />
          </IconButton>
        </DialogTitle>
        <DialogContent className={classes.dialogContent}>
          <Grid container spacing={2}>
            {
              visitorData.map((visit, index) => {
                return (
                  <React.Fragment key={index.toString()}>
                    <Grid item xs={12} sm={6}>
                      <FormControl error={!!errors.name} className={classes.inputField}>
                        <TextField
                          error={!!errors.name}
                          fullWidth
                          label="Nome do visitante"
                          margin="normal"
                          InputLabelProps={{ shrink: true }}
                          value={visitorData[index].name}
                          disabled={isDisabled}
                          onChange={(e) => onChangeVisitorName(e, index)}
                        />
                        {errors.name && <FormHelperText>{errors.name}</FormHelperText>}
                      </FormControl>
                    </Grid>
                    <Grid item xs={10} sm={5}>
                      <FormControl error={!!errors.email} className={classes.inputField}>
                        <TextField
                          error={!!errors.email}
                          fullWidth
                          label="E-mail do visitante"
                          margin="normal"
                          InputLabelProps={{ shrink: true }}
                          value={visitorData[index].email}
                          disabled={isDisabled}
                          onChange={(e) => onChangeVisitorEmail(e, index)}
                        />
                        {errors.email && <FormHelperText>{errors.email}</FormHelperText>}
                      </FormControl>
                    </Grid>

                    <Grid item xs={2} sm={1}>
                      <div className={classes.buttonContainer}>
                        {
                          index ?
                            <Button
                              tabIndex="-1"
                              onClick={() => onRemoveVisitor(index)}
                              variant="contained"
                              disabled={isDisabled}
                              className={classes.deleteVisitorButton}
                            >
                              -
                            </Button>
                            :
                            <Button
                              onClick={onAddVisitor}
                              color="primary"
                              variant="contained"
                              disabled={isDisabled}
                              className={classes.addVisitorButton}
                            >
                              +
                            </Button>
                        }
                      </div>
                    </Grid>
                  </React.Fragment>
                );
              })
            }
            <React.Fragment>
              <Grid item xs={12} sm={6}>
                <FormControl className={classes.inputField} error={!!errors.visitorCompany}>
                  <TextField
                    error={!!errors.visitorCompany}
                    fullWidth
                    label="Empresa do visitante/evento"
                    margin="normal"
                    InputLabelProps={{ shrink: true }}
                    value={visitorCompany}
                    disabled={isDisabled}
                    onChange={e => setMemberState({ visitorCompany: e.target.value })}
                  />
                  {
                    errors.visitorCompany &&
                      <FormHelperText>{errors.visitorCompany}</FormHelperText>
                  }
                </FormControl>
              </Grid>
              <Grid item xs={12} sm={6}>
                <FormControl className={classes.inputField} error={!!errors.host_member}>
                  <InputLabel shrink className={classes.inputLabel}>Pessoa visitada</InputLabel>
                  <NativeSelect
                    fullWidth
                    className={classes.selectBox}
                    value={hostMemberId}
                    disabled={isDisabled}
                    onChange={e => setMemberState({ hostMemberId: e.target.value })}
                  >
                    {members.map((member) => (
                      <option key={member.id} value={member.id}>
                        {`${member.name} ${member.lastname}`}
                      </option>
                    ))}
                  </NativeSelect>
                  {errors.members && <FormHelperText>{errors.members}</FormHelperText>}
                </FormControl>
              </Grid>
            </React.Fragment>
            <React.Fragment>
              <Grid item xs={6}>
                <FormControl className={classes.inputField} error={!!errors.visitorCompany}>
                  <MuiPickersUtilsProvider
                    libInstance={moment}
                    utils={MomentUtils}
                    locale={config.locale}
                  >
                    <DatePicker
                      className={classes.calendarInputField}
                      style={styles.components}
                      label="Data da visita"
                      invalidDateMessage="Formato invlido"
                      variant="dialog"
                      value={visitDate}
                      disabled={isDisabled}
                      onChange={e => setMemberState({ visitDate: e })}
                      minDate={new Date()}
                      format="DD/MM/YYYY"
                      cancelLabel="Cancelar"
                    />
                  </MuiPickersUtilsProvider>
                </FormControl>
              </Grid>
              <Grid item xs={6}>
                <FormControl className={classes.inputField} error={!!errors.visitorCompany}>
                  <MuiPickersUtilsProvider
                    libInstance={moment}
                    utils={MomentUtils}
                    locale={config.locale}
                  >
                    <TimePicker
                      className={classes.calendarInputField}
                      style={styles.components}
                      ampm={false}
                      minutesStep={5}
                      variant="dialog"
                      label="Horrio"
                      value={visitTime}
                      disabled={isDisabled}
                      onChange={e => setMemberState({ visitTime: e })}
                      cancelLabel="Cancelar"
                    />
                  </MuiPickersUtilsProvider>
                </FormControl>
              </Grid>
              <Grid item xs={12} sm={6}>
                <FormControl className={classes.inputField}>
                  <InputLabel shrink className={classes.inputLabel}>Prdio</InputLabel>
                  <NativeSelect
                    fullWidth
                    className={classes.selectBox}
                    value={buildingId}
                    disabled={isDisabled}
                    onChange={e => setMemberState({ buildingId: e.target.value })}
                  >
                    {buildings.map((b) => (
                      <option key={b.id} value={b.id}>{b.name}</option>
                    ))}
                  </NativeSelect>
                </FormControl>
              </Grid>
            </React.Fragment>
          </Grid>
        </DialogContent>
        <DialogActions className={classes.actions}>
          <Button onClick={handleClose} size="large" disabled={isDisabled}>
            Cancelar
          </Button>
          <Button
            onClick={() => submitFetch(true)}
            size="large"
            color="primary"
            variant="contained"
            disabled={isDisabled}
            style={styles.submitButton}
          >
            {submitLoading ? 'Autorizando...' : 'Autorizar'}
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
});

VisitorModal.propTypes = {
  classes: PropTypes.object,
  open: PropTypes.bool,
  setOpen: PropTypes.func,
  members: PropTypes.array,
  setMemberState: PropTypes.func,
  buildings: PropTypes.array,
  memberData: PropTypes.object,
  submitFetch: PropTypes.func,
  submitLoading: PropTypes.bool,
  submitError: PropTypes.object,
  loading: PropTypes.bool
};

const withCreateVisitor = withRequest(VisitorsService.createVisitors, {
  name: 'submit',
  init: false,
  options: ({ onCreateVisitor, memberData, setOpen, setMemberState, companyId }) => ({
    variables: { ...memberData, companyId },
    onRequestFinished: () => {
      Tracker.hit('NewVisitor - Added');
      setOpen(false);
      setMemberState(getInitialMemberData());
      onCreateVisitor();
    },
    onRequestError: (err) => {
      onCreateVisitor(err);
    }
  })
});

export default compose(
  withStyles(styles, { withTheme: true }),
  withState('open', 'setOpen', false),
  withState('memberData', 'setMemberData', getInitialMemberData()),
  withHandlers({
    setMemberState: ({ setMemberData, memberData }) => (patch) => {
      setMemberData({ ...memberData, ...patch });
    }
  }),
  withCreateVisitor
)(VisitorModal);
