import React, { PureComponent } from 'react';
import { Link } from 'react-router-dom';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';
import styles from './styles';
import config from '../../config';
import PasswordService from '../../services/password';
import AlertBox from '../../components/AlertBox';

class ResetPassword extends PureComponent {
  state = {
    email: '',
    msg: '',
    msgType: '',
    submit: false
  }

  onSubmit = (e) => {
    e.preventDefault();
    const { email } = this.state;
    this.setState({ submit: true }, async () => {
      try {
        if (email) {
          await PasswordService.resetPassword(email);
          this.setState({
            submit: false,
            email: '',
            msgType: 'success',
            msg: 'Enviamos para seu email um link de recuperao de senha.'
          });
          return true;
        }
        throw new Error('Blank fields');
      } catch {
        this.setState({ submit: false, msg: 'Email invlido ou inexistente.', msgType: 'warning' });
        return true;
      }
    });
  }

  render() {
    const { email, submit, msg, msgType } = this.state;
    return (
      <div style={styles.container}>
        <div style={styles.content}>
          {msg && <AlertBox message={msg} type={msgType} onClose={() => this.setState({ msg: '', msgType: '' })} />}
          <img src={require('../../assets/images/logo.png')} width={150} alt={config.appName} style={styles.logo} />
          <Typography component="h1" variant="h5">
            Recuperao de senha
          </Typography>
          <form onSubmit={this.onSubmit} style={styles.form}>
            <TextField
              variant="outlined"
              margin="normal"
              fullWidth
              placeholder="Informe seu email de acesso"
              label="Email"
              type="email"
              disabled={submit}
              value={email}
              InputLabelProps={{ shrink: true }}
              onChange={e => this.setState({ email: e.target.value.toLowerCase() })}
            />
            <Button
              disabled={submit}
              fullWidth
              variant="contained"
              color="primary"
              size="large"
              style={styles.button}
              type="submit"
            >
              {submit ? 'Carregando...' : 'Enviar'}
            </Button>
          </form>
          <p>
            <Link to="/">Voltar para home</Link>
          </p>
        </div>
      </div>
    );
  }
}

export default ResetPassword;
