import React, { memo } from 'react';
import PropTypes from 'prop-types';
import moment from 'moment';
import _get from 'lodash/get';
import CloseIcon from '@material-ui/icons/Close';
import { TableCell, TableRow } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import styles from './styles';

const STATUS = {
  ACCEPTED: 'Autorizado',
  PENDING: 'Pendente',
  CANCELED: 'Cancelado'
};

const VisitorsItemRow = memo(({ classes, index, visitor, onCancelVisitor }) => {
  const visitorId = _get(visitor, 'id');
  const visitorFullName = `${_get(visitor, 'name')} ${_get(visitor, 'visitor_company') || ''}`;
  const hostMemberFullName = `${_get(visitor, 'host_member.name')} ${_get(visitor, 'host_member.lastname')}`;
  const hostMemberEmail = _get(visitor, 'host_member.email');
  const inviterFullName = `${_get(visitor, 'inviter.name')} ${_get(visitor, 'inviter.lastname')}`;
  const inviterEmail = _get(visitor, 'inviter.email');
  const visitDate = _get(visitor, 'visit_date');
  const visitHour = moment(_get(visitor, 'visit_hour'));
  const checkinAt = _get(visitor, 'checkin_at');
  const visitPeriod = moment(visitDate).hour(visitHour.hour()).minute(visitHour.minute());
  return (
    <TableRow className={index % 2 ? classes.bgWhite : classes.bgGray}>
      <TableCell className={classes.tableCell}>
        {visitDate ? `${moment(visitDate).format('DD/MM/YYYY')}` : '--'}
        <br />
        {visitHour ? `${moment(visitHour).format('HH:mm')}` : '--'}
      </TableCell>
      <TableCell className={classes.tableCell}>
        {STATUS[visitor.status]}
        <br />
        {checkinAt ? moment(checkinAt).format('DD/MM/YY HH:mm') : ''}
      </TableCell>
      <TableCell className={classes.tableCell}>
        {visitorFullName}
      </TableCell>
      <TableCell className={classes.tableCell}>
        {hostMemberFullName}
        <br />
        <small>{hostMemberEmail}</small>
      </TableCell>
      <TableCell className={classes.tableCell}>
        {inviterFullName}
        <br />
        <small>{inviterEmail}</small>
      </TableCell>
      <TableCell className={classes.tableSmallCell}>
        {
          (visitor.status !== 'ACCEPTED') &&
          (moment(visitPeriod).isAfter(moment().startOf('day'))) &&
          <button
            type="button"
            className={classes.clearButton}
            onClick={() => onCancelVisitor(visitorId)}
          >
            <CloseIcon />
          </button>
        }
      </TableCell>
    </TableRow>
  );
});

VisitorsItemRow.propTypes = {
  index: PropTypes.number,
  visitor: PropTypes.object,
  classes: PropTypes.object,
  onCancelVisitor: PropTypes.func
};

export default withStyles(styles, { withTheme: true })(VisitorsItemRow);
