/* global document */
import 'moment/locale/pt-br';
import React, { Suspense, PureComponent } from 'react';
import ReactDOM from 'react-dom';
import { HashRouter, Redirect, Route, Switch } from 'react-router-dom';
import _get from 'lodash/get';
import _map from 'lodash/map';
import _flatten from 'lodash/flatten';
import moment from 'moment';
import CssBaseline from '@material-ui/core/CssBaseline';
import { MuiThemeProvider } from '@material-ui/core/styles';
import EnterpriseUserService from './services/enterprise-user';
import Session from './utils/session';
import withTracker from './utils/with-tracker';
import Tracker from './utils/tracker';
import Store from './utils/store';
import Loading from './components/Loading';
import theme from './theme';
import config from './config';
import Routes from './routes';
import * as Pages from './pages';

moment.locale(config.locale);

const flatRoutes = _flatten(_map(Routes, (r, rkey) => {
  if (r.submenus) {
    return _map(r.submenus, (rs, rskey) => ({
      ...rs,
      path: `${r.path}${rs.path}`,
      key: rskey,
      page: Pages[rskey]
    }));
  }
  return { ...r, key: rkey, page: Pages[rkey] };
}));

class App extends PureComponent {
  state = {
    loading: true
  }

  componentDidMount() {
    if (config.isiOS) {
      document.querySelector('link[rel="manifest"]').setAttribute('rel', 'no-on-ios');
    }
    this.setState({ loading: true }, async () => {
      try {
        const enterpriseUserResponse = await EnterpriseUserService.getEnterpriseUser();
        const enterpriseUser = _get(enterpriseUserResponse, 'enterprise_user');
        Session.setUserData(enterpriseUser);
        Tracker.trackScreenView('Home');
      } catch {
        Session.clear();
      } finally {
        this.setState({ loading: false });
      }
    });
  }

  render() {
    const { loading } = this.state;
    return (
      <MuiThemeProvider theme={theme}>
        <CssBaseline />
        <Suspense fallback={<Loading hasRefreshMsg />}>
          {
            loading ?
              <Loading hasRefreshMsg />
              :
              <HashRouter>
                <Switch>
                  {
                    _map(flatRoutes, (r) => (
                      <Route
                        key={r.path}
                        exact={r.exact}
                        path={r.path}
                        render={(p) => {
                          try {
                            if ((r.private && Session.isLoggedIn()) || !r.private) {
                              const TrackedPage = withTracker(r.page, r.trackPage);
                              return <TrackedPage {...p} />;
                            }
                            const redirectUrl = `${p.location.pathname}${p.location.search}`;
                            Store.save('redirectUrl', redirectUrl);
                            return <Redirect to={{ pathname: '/', state: { from: p.location } }} />;
                          } catch {
                            return <Redirect to={{ pathname: '/', state: { from: p.location } }} />;
                          }
                        }}
                      />
                    ))
                  }
                </Switch>
              </HashRouter>
          }
        </Suspense>
      </MuiThemeProvider>
    );
  }
}

ReactDOM.render(<App />, document.querySelector('main'));
