/* global window */
import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import CircularProgress from '@material-ui/core/CircularProgress';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import { withStyles } from '@material-ui/core/styles';
import styles from './styles';
import config from '../../config';

class Loading extends PureComponent {
  state = {
    slowConnection: false
  }

  componentDidMount() {
    const { hasRefreshMsg } = this.props;
    if (hasRefreshMsg) {
      this.timeoutConnection = setTimeout(() => {
        this.setState({ slowConnection: true });
      }, config.apiRequestTimeout);
    }
  }

  componentWillUnmount() {
    clearTimeout(this.timeoutConnection);
  }

  render() {
    const { hasRefreshMsg, classes, theme: { mainColor } } = this.props;
    const { slowConnection } = this.state;
    return (
      <Grid
        container
        direction="column"
        justify="center"
        alignItems="center"
        className={classes.middle}
      >
        <CircularProgress
          color={mainColor}
          className={classes.progress}
          variant="indeterminate"
          disableShrink
          thickness={5}
        />
        <b>Carregando...</b>
        {
          (hasRefreshMsg && slowConnection) ?
            <div className={classes.center}>
              <br />
              <b>H problemas de conexo com a internet</b>
              <Button
                onClick={() => window.location.reload()}
                size="large"
                color="primary"
                variant="contained"
              >
                <b>Recarregar</b>
              </Button>
            </div>
            :
            null
        }
      </Grid>
    );
  }
}

Loading.propTypes = {
  classes: PropTypes.object,
  theme: PropTypes.object,
  hasRefreshMsg: PropTypes.bool
};

export default withStyles(styles, { withTheme: true })(Loading);
