import './custom.css';
import 'react-big-calendar/lib/css/react-big-calendar.css';
import React, { memo } from 'react';
import PropTypes from 'prop-types';
import { Calendar, momentLocalizer, Views } from 'react-big-calendar';
import Toolbar from 'react-big-calendar/lib/Toolbar';
import moment from 'moment';
import styles from './styles';
import config from '../../config';

const localizer = momentLocalizer(moment);
const VIEWS = [
  { key: Views.WEEK, value: 'Semanal' },
  { key: Views.MONTH, value: 'Mensal' }
];

class AppToolbar extends Toolbar {
  render() {
    const { label, onNavigate, view, loading } = this.props;
    return (
      <div className="rbc-toolbar">
        <div className="rbc-btn-group">
          {VIEWS.map(({ key, value }) => {
            const className = view === key ? 'rbc-active' : '';
            const onClick = this.view.bind(null, key);
            return (
              <button
                key={key}
                type="button"
                disabled={loading}
                className={className}
                onClick={onClick}
              >
                {value}
              </button>
            );
          })}
        </div>
        <b className="rbc-toolbar-label">{loading ? 'Carregando...' : label}</b>
        <div className="rbc-btn-group">
          <button type="button" disabled={loading} onClick={() => onNavigate('PREV')}>&larr;</button>
          <button type="button" disabled={loading} onClick={() => onNavigate('TODAY')}>Hoje</button>
          <button type="button" disabled={loading} onClick={() => onNavigate('NEXT')}>&rarr;</button>
        </div>
      </div>
    );
  }
}

AppToolbar.propTypes = {
  loading: PropTypes.bool
};

const eventBoxStyle = (event) => {
  const isPastBooking = moment(event.bookingDate).isBefore();
  if (isPastBooking) {
    return styles.pastBox;
  }
  if (event.busy) {
    return styles.busyBox;
  }
  return styles.ownerBox;
};

const AppCalendar = memo(({
  data, view, onView, onRangeChange,
  loading, eventComponent, onSelectBooking
}) => (
  <Calendar
    resizable
    events={data}
    localizer={localizer}
    view={view}
    onView={onView}
    startAccessor="from"
    endAccessor="to"
    step={30}
    onRangeChange={onRangeChange}
    scrollToTime={moment(6, 'HH').toDate()}
    culture={config.locale}
    eventPropGetter={e => ({ style: eventBoxStyle(e) })}
    components={{
      event: eventComponent,
      toolbar: (props) => <AppToolbar loading={loading} {...props} />
    }}
    style={styles.calendar}
    onSelectEvent={onSelectBooking}
  />
));

AppCalendar.propTypes = {
  data: PropTypes.array,
  eventComponent: PropTypes.func,
  onView: PropTypes.func,
  view: PropTypes.string,
  loading: PropTypes.bool,
  onSelectBooking: PropTypes.func,
  onRangeChange: PropTypes.func
};

export default AppCalendar;
