import React, { memo } from 'react';
import _get from 'lodash/get';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import { compose, withState } from 'recompose';
import Header from '../../components/Header';
import AlertBox from '../../components/AlertBox';
import SettingsMenu from '../../components/SettingsMenu';
import withRequest from '../../utils/with-request';
import MembersService from '../../services/members';
import Session from '../../utils/session';
import MembersTable from './MembersTable';
import styles from './styles';

const Members = memo(({
  membersData, membersLoading, membersError,
  alert, setAlert, membersFetch, classes
}) => {
  const { showAlert, alertMsg, alertType } = alert;
  const onClearAlert = () => setAlert({ alertMsg: '', alertType: '', showAlert: false });
  let alertBox = null;
  if (showAlert) {
    alertBox = <AlertBox message={alertMsg} type={alertType} onClose={onClearAlert} />;
  }
  const enterpriseUser = Session.getUserData();
  const loggedMemberId = _get(enterpriseUser, 'member.id');
  const members = _get(membersData, 'members');
  return (
    <div className={classes.root}>
      <Header alertBox={alertBox} error={membersError} />
      <section>
        <div className={classes.container}>
          <div className={classes.headerView}>
            <aside>
              <Typography variant="h5">
                <b>Configuraes</b>
              </Typography>
            </aside>
          </div>
        </div>
        <div className={classes.container}>
          <SettingsMenu />
        </div>
        <div className={classes.container}>
          <MembersTable
            loggedMemberId={loggedMemberId}
            refetch={(showLoadingIndicator = false) => membersFetch(showLoadingIndicator)}
            setAlert={setAlert}
            members={members}
            loading={membersLoading}
          />
        </div>
      </section>
    </div>
  );
});

Members.propTypes = {
  classes: PropTypes.object,
  membersLoading: PropTypes.bool,
  membersError: PropTypes.object,
  membersData: PropTypes.object,
  membersFetch: PropTypes.func,
  alert: PropTypes.object,
  setAlert: PropTypes.func
};

const withMembers = withRequest(MembersService.getMembers, {
  name: 'members'
});

export default compose(
  withStyles(styles, { withTheme: true }),
  withState('alert', 'setAlert', {
    alertMsg: '',
    alertType: '',
    showAlert: false
  }),
  withMembers
)(Members);
