import React, { memo } from 'react';
import PropTypes from 'prop-types';
import moment from 'moment';
import _get from 'lodash/get';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import AvatarImage from '../../../../components/AvatarImage';
import styles from './styles';

const TicketComment = memo(({ comment, classes }) => (
  <div>
    <div className={classes.userNameContainer}>
      <AvatarImage name={_get(comment, 'member.name')} />
      <Typography variant="subtitle2" component="b" className={classes.userName}>
        {`${_get(comment, 'member.name')} ${_get(comment, 'member.lastname')}`}
      </Typography>
    </div>
    <div className={classes.content}>
      <div className={classes.baloon}>
        <span>{comment.content}</span>
      </div>
      <Typography variant="body2" color="textSecondary" component="small">
        {moment(_get(comment, 'created_at')).calendar()}
      </Typography>
    </div>
  </div>
));

TicketComment.propTypes = {
  classes: PropTypes.object,
  comment: PropTypes.object
};

export default withStyles(styles, { withTheme: true })(TicketComment);
