/* global window */
import React, { memo } from 'react';
import _concat from 'lodash/concat';
import _size from 'lodash/size';
import _get from 'lodash/get';
import _times from 'lodash/times';
import _isEmpty from 'lodash/isEmpty';
import _uniqBy from 'lodash/uniqBy';
import _sortBy from 'lodash/sortBy';
import moment from 'moment';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import FormControl from '@material-ui/core/FormControl';
import PropTypes from 'prop-types';
import { compose, withState } from 'recompose';
import Header from '../../components/Header';
import AlertBox from '../../components/AlertBox';
import withRequest from '../../utils/with-request';
import Session from '../../utils/session';
import Tracker from '../../utils/tracker';
import VisitorsService from '../../services/visitors';
import MembersService from '../../services/members';
import VisitorModal from './VisitorModal';
import VisitorsTable from './VisitorsTable';
import styles from './styles';

const LAST_MONTHS = 6;
const PER_PAGE = 20;
const DATE_FMKEY = 'YYYY-MM';
const DATE_FMLABEL = 'MMMM YYYY';

const Visitors = memo(({
  alert, setAlert, setBuildingId, setMonth, setYear, setVisitors,
  setDisableSeeMore, membersData, visitors, page, setPage, buildingId,
  setHideSeeMore, visitorsFetch, visitorsLoading, visitorsError,
  disableSeeMore, classes, month, year, hideSeeMore, totalVisitors
}) => {
  const onSelectMonth = async (e) => {
    const selectedDate = moment(e.target.value, DATE_FMKEY);
    setMonth(selectedDate.month());
    setYear(selectedDate.year());
    onGetVisitors();
    const trackProps = {
      select_month: selectedDate.month() + 1,
      select_year: selectedDate.year()
    };
    Tracker.hit('Visitors - FilteredByMonth', trackProps);
  };

  const onSelectBuilding = async (e) => {
    setBuildingId(e.target.value);
    onGetVisitors();
    const trackProps = {
      select_building: e.target.value
    };
    Tracker.hit('Visitors - FilteredByBuilding', trackProps);
  };

  const onGetVisitors = async () => {
    try {
      setDisableSeeMore(true);
      setPage(0);
      const visitorsData = await visitorsFetch(true);
      const newVisitors = _uniqBy(_get(visitorsData, 'visitors', []), 'id');
      setVisitors(newVisitors);
      setDisableSeeMore(false);
      setHideSeeMore(_isEmpty(newVisitors));
    } catch {
      setDisableSeeMore(false);
      setHideSeeMore(true);
    }
  };

  const onSeeMore = async () => {
    try {
      setDisableSeeMore(true);
      setPage(page + 1);
      const visitorsData = await visitorsFetch(false);
      const newVisitors = _get(visitorsData, 'visitors', []);
      setVisitors(_uniqBy(_concat(visitors, newVisitors), 'id'));
      setDisableSeeMore(false);
      setHideSeeMore(_isEmpty(newVisitors));
    } catch {
      setDisableSeeMore(false);
      setVisitors(visitors);
      setHideSeeMore(true);
    }
  };

  const onCancelVisitor = async (visitorId) => {
    if (window.confirm('Deseja cancelar a visita?')) {
      try {
        await VisitorsService.deleteVisitor(visitorId);
        await visitorsFetch(true);
        setAlert({ alertMsg: 'Visitante cancelado com sucesso', alertType: 'success', showAlert: true });
      } catch {
        setAlert({ alertMsg: 'Ocorreu um erro no cancelamento da visita', alertType: 'error', showAlert: true });
      }
    }
  };

  const onCreateVisitor = async (err) => {
    if (err) {
      setAlert({ alertMsg: 'Ocorreu um erro ao registrar visitante', alertType: 'error', showAlert: true });
    } else {
      try {
        setAlert({ alertMsg: 'Visitante autorizado com sucesso', alertType: 'success', showAlert: true });
        await visitorsFetch(true);
      } catch {
        setAlert({ alertMsg: 'Ocorreu um erro atualizar lista de visitantes', alertType: 'error', showAlert: true });
      }
    }
  };

  const canShowMore = Boolean(!visitorsLoading && !((_size(visitors) || 1) === totalVisitors));
  const enterpriseUser = Session.getUserData();
  const companyId = _get(enterpriseUser, 'member.company.id');
  const buildings = _sortBy(_get(enterpriseUser, 'buildings', []), ['name']);
  const { showAlert, alertMsg, alertType } = alert;
  const onClearAlert = () => setAlert({ alertMsg: '', alertType: '', showAlert: false });
  let alertBox = null;
  if (showAlert) {
    alertBox = <AlertBox message={alertMsg} type={alertType} onClose={onClearAlert} />;
  }
  const members = _get(membersData, 'members', []);
  const isLoading = visitorsLoading;
  const isDisabled = isLoading;
  return (
    <div className={classes.root}>
      <Header alertBox={alertBox} error={visitorsError} />
      <section>
        <div className={classes.container}>
          <div className={classes.headerView}>
            <aside>
              <Typography variant="h5">
                <b>{totalVisitors ? `Visitantes (${totalVisitors})` : 'Visitantes' }</b>
              </Typography>
              <Grid container spacing={2}>
                <Grid item xs={12} sm={6}>
                  <FormControl fullWidth>
                    <TextField
                      select
                      label="Prdio"
                      className={classes.selectBox}
                      disabled={isDisabled}
                      value={buildingId}
                      SelectProps={{ native: true }}
                      InputLabelProps={{ shrink: true }}
                      onChange={onSelectBuilding}
                    >
                      {buildings.map(b => <option key={b.id} value={b.id}>{b.name}</option>)}
                    </TextField>
                  </FormControl>
                </Grid>
                <Grid item xs={12} sm={6}>
                  <FormControl fullWidth>
                    <TextField
                      select
                      label="Ms"
                      className={classes.selectBox}
                      disabled={isDisabled}
                      value={moment().month(month).year(year).format(DATE_FMKEY)}
                      SelectProps={{ native: true }}
                      InputLabelProps={{ shrink: true }}
                      onChange={onSelectMonth}
                    >
                      {
                        _times(LAST_MONTHS, (t) => {
                          const selectedDate = moment().subtract(t, 'month');
                          return (
                            <option
                              key={selectedDate.format(DATE_FMKEY)}
                              value={selectedDate.format(DATE_FMKEY)}
                            >
                              {selectedDate.format(DATE_FMLABEL)}
                            </option>
                          );
                        })
                      }
                    </TextField>
                  </FormControl>
                </Grid>
              </Grid>
            </aside>
            <aside>
              <VisitorModal
                loading={isLoading}
                setAlert={setAlert}
                members={members}
                companyId={companyId}
                onCreateVisitor={onCreateVisitor}
                buildings={buildings}
              />
            </aside>
          </div>
        </div>
        <div className={classes.container}>
          <VisitorsTable
            visitors={visitors}
            loading={visitorsLoading}
            onCancelVisitor={onCancelVisitor}
          />
          {
            canShowMore && !hideSeeMore &&
              <div className={classes.buttonContainer}>
                <Button
                  className={classes.showMoreButton}
                  onClick={onSeeMore}
                  size="large"
                  variant="contained"
                  color="primary"
                  disabled={disableSeeMore}
                >
                  {disableSeeMore ? 'Carregando' : 'Ver Mais'}
                </Button>
              </div>
          }
        </div>
      </section>
    </div>
  );
});

Visitors.propTypes = {
  classes: PropTypes.object,
  visitorsLoading: PropTypes.bool,
  visitorsFetch: PropTypes.func,
  visitorsError: PropTypes.object,
  buildingId: PropTypes.string,
  visitors: PropTypes.array,
  membersData: PropTypes.object,
  setVisitors: PropTypes.func,
  setBuildingId: PropTypes.func,
  setMonth: PropTypes.func,
  setYear: PropTypes.func,
  alert: PropTypes.object,
  setAlert: PropTypes.func,
  totalVisitors: PropTypes.number,
  month: PropTypes.number,
  year: PropTypes.number,
  setPage: PropTypes.func,
  page: PropTypes.number,
  setDisableSeeMore: PropTypes.func,
  disableSeeMore: PropTypes.bool,
  setHideSeeMore: PropTypes.func,
  hideSeeMore: PropTypes.bool
};

const withMembers = withRequest(MembersService.getMembers, {
  name: 'members'
});

const withVisitors = withRequest(VisitorsService.getVisitors, {
  name: 'visitors',
  options: ({ page, month, year, buildingId, setVisitors, setHideSeeMore, setTotalVisitors }) => ({
    variables: { page, perPage: PER_PAGE, month: month + 1, year, buildingId },
    onRequestFinished: (data) => {
      const visitors = _uniqBy(_get(data, 'visitors', []), 'id');
      const totalVisitors = _get(data, 'meta.total', 0);
      setVisitors(visitors);
      setHideSeeMore(_isEmpty(visitors));
      setTotalVisitors(totalVisitors);
    }
  })
});

export default compose(
  withStyles(styles, { withTheme: true }),
  withState('alert', 'setAlert', {
    alertMsg: '',
    alertType: '',
    showAlert: false
  }),
  withState('buildingId', 'setBuildingId', _get(Session.getUserData(), 'buildings[0].id')),
  withState('totalVisitors', 'setTotalVisitors', 0),
  withState('visitors', 'setVisitors', []),
  withState('hideSeeMore', 'setHideSeeMore', false),
  withState('disableSeeMore', 'setDisableSeeMore', false),
  withState('page', 'setPage', 0),
  withState('month', 'setMonth', moment().month()),
  withState('year', 'setYear', moment().year()),
  withMembers,
  withVisitors
)(Visitors);
