import React, { memo } from 'react';
import _get from 'lodash/get';
import _mapValues from 'lodash/mapValues';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import FormControl from '@material-ui/core/FormControl';
import FormHelperText from '@material-ui/core/FormHelperText';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';
import PropTypes from 'prop-types';
import { compose, withState } from 'recompose';
import Header from '../../components/Header';
import AlertBox from '../../components/AlertBox';
import SettingsMenu from '../../components/SettingsMenu';
import withRequest from '../../utils/with-request';
import Member from '../../services/member';
import styles from './styles';

const ChangePassword = memo(({
  alert, setAlert, classes, password,
  confirmPassword, setPassword, setConfirmPassword,
  updateMemberFetch, updateMemberError, updateMemberLoading
}) => {
  const { showAlert, alertMsg, alertType } = alert;
  const onClearAlert = () => setAlert({ alertMsg: '', alertType: '', showAlert: false });
  const onSubmit = () => {
    if (password === confirmPassword) {
      updateMemberFetch(true);
    } else {
      setAlert({ alertMsg: 'As senhas esto diferentes', alertType: 'error', showAlert: true });
    }
  };
  const errors = _mapValues(_get(updateMemberError, 'errors', {}), (err) => err[0]);
  let alertBox = null;
  if (showAlert) {
    alertBox = <AlertBox message={alertMsg} type={alertType} onClose={onClearAlert} />;
  }
  const isDisabled = updateMemberLoading;
  return (
    <div className={classes.root}>
      <Header alertBox={alertBox} />
      <section>
        <div className={classes.container}>
          <div className={classes.headerView}>
            <aside>
              <Typography variant="h5">
                <b>Configuraes</b>
              </Typography>
            </aside>
          </div>
        </div>
        <div className={classes.container}>
          <SettingsMenu />
        </div>
        <br />
        <div className={classes.container}>
          <Paper className={classes.paper}>
            <Grid container item xs={12} sm={6} md={4}>
              <Grid item xs={12}>
                <FormControl fullWidth error={!!errors.password}>
                  <TextField
                    label="Nova senha"
                    margin="normal"
                    type="password"
                    variant="outlined"
                    className={classes.input}
                    InputLabelProps={{ shrink: true }}
                    disabled={isDisabled}
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                  />
                  {errors.password && <FormHelperText>{errors.password}</FormHelperText>}
                </FormControl>
              </Grid>
              <Grid item xs={12}>
                <FormControl fullWidth>
                  <TextField
                    label="Confirmar nova senha"
                    margin="normal"
                    type="password"
                    variant="outlined"
                    className={classes.input}
                    InputLabelProps={{ shrink: true }}
                    disabled={isDisabled}
                    value={confirmPassword}
                    onChange={(e) => setConfirmPassword(e.target.value)}
                  />
                </FormControl>
              </Grid>
              <Grid item xs={12}>
                <div className={classes.actions}>
                  <Button
                    onClick={onSubmit}
                    size="large"
                    color="primary"
                    variant="contained"
                    disabled={isDisabled}
                  >
                    Atualizar
                  </Button>
                </div>
              </Grid>
            </Grid>
          </Paper>
        </div>
      </section>
    </div>
  );
});

ChangePassword.propTypes = {
  classes: PropTypes.object,
  password: PropTypes.string,
  setPassword: PropTypes.func,
  confirmPassword: PropTypes.string,
  setConfirmPassword: PropTypes.func,
  updateMemberLoading: PropTypes.bool,
  updateMemberFetch: PropTypes.func,
  updateMemberError: PropTypes.func,
  alert: PropTypes.object,
  setAlert: PropTypes.func
};

const withUpdateMember = withRequest(Member.updateMemberPassword, {
  name: 'updateMember',
  init: false,
  options: ({ password, setAlert, setPassword, setConfirmPassword }) => ({
    variables: { password },
    onRequestFinished: () => {
      setAlert({ alertMsg: 'Perfil atualizado com sucesso', alertType: 'success', showAlert: true });
      setPassword('');
      setConfirmPassword('');
    },
    onRequestError: () => {
      setAlert({ alertMsg: 'Erro ao atualizar perfil', alertType: 'error', showAlert: true });
    }
  })
});

export default compose(
  withStyles(styles, { withTheme: true }),
  withState('alert', 'setAlert', {
    alertMsg: '',
    alertType: '',
    showAlert: false
  }),
  withState('password', 'setPassword', ''),
  withState('confirmPassword', 'setConfirmPassword', ''),
  withUpdateMember
)(ChangePassword);
