/* global window */
import React, { memo } from 'react';
import {
  get as _get, map as _map,
  pickBy as _pickBy, isEqual as _isEqual,
  mapValues as _mapValues
} from 'lodash';
import {
  compose, withState, withHandlers
} from 'recompose';
import { withStyles, useTheme } from '@material-ui/core/styles';
import {
  useMediaQuery,
  Dialog,
  DialogContent,
  DialogActions,
  DialogTitle,
  IconButton,
  Grid,
  Button
} from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';
import PropTypes from 'prop-types';

import MembersService from '../../../services/members';
import withRequest from '../../../utils/with-request';
import Tracker from '../../../utils/tracker';
import MemberForm from './MemberForm';
import styles from './styles';

const MemberFormModal = memo(({
  member, isLoading: isLoadingProp, open, onClose,
  classes,
  formData, setFormData,
  updateMemberFetch, updateMemberLoading,
  blockMemberFetch, blockMemberLoading
}) => {
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));
  const handleBlockMember = () => {
    if (window.confirm('Tem certeza em bloquear esse usurio?')) {
      blockMemberFetch(true, '', null, member.id);
    }
  };
  const isLoading = (isLoadingProp || updateMemberLoading || blockMemberLoading);

  return (
    <Dialog
      fullScreen={fullScreen}
      keepMounted
      fullWidth
      maxWidth="md"
      classes={{ paper: classes.dialogMemberForm }}
      open={open}
      onClose={() => onClose({ shouldRefetch: false })}
    >
      <DialogTitle>
        Editar usurio
        <IconButton className={classes.closeButton} onClick={onClose}>
          <CloseIcon />
        </IconButton>
      </DialogTitle>
      <DialogContent className={classes.content}>
        <MemberForm
          member={member}
          formData={formData}
          setFormData={setFormData}
          isLoading={isLoading}
        />
      </DialogContent>
      <DialogActions className={classes.actions}>
        <Grid container justify="space-between" spacing={2}>
          <Grid item xs={12} sm={8}>
            <Button
              fullWidth={fullScreen}
              color="secondary"
              size="large"
              variant={fullScreen ? 'outlined' : 'contained'}
              onClick={handleBlockMember}
              disabled={isLoading}
            >
              DESATIVAR USURIO
            </Button>
          </Grid>
          <Grid item xs={6} sm={2}>
            <Button
              fullWidth
              color="default"
              size="large"
              variant="contained"
              onClick={() => onClose({ shouldRefetch: false })}
              disabled={isLoading}
            >
              CANCELAR
            </Button>
          </Grid>
          <Grid item xs={6} sm={2}>
            <Button
              fullWidth
              color="primary"
              size="large"
              variant="contained"
              onClick={() => updateMemberFetch(true).catch(() => {})}
              disabled={isLoading}
            >
              SALVAR
            </Button>
          </Grid>
        </Grid>
      </DialogActions>
    </Dialog>
  );
});
MemberFormModal.propTypes = {
  member: PropTypes.object,
  isLoading: PropTypes.bool,
  open: PropTypes.bool,
  onClose: PropTypes.func,
  classes: PropTypes.object,
  formData: PropTypes.object,
  setFormData: PropTypes.func,
  updateMemberFetch: PropTypes.func,
  updateMemberLoading: PropTypes.bool,
  blockMemberFetch: PropTypes.func,
  blockMemberLoading: PropTypes.bool
};

const initialFormData = {
  buildingsOptions: [], previousValues: {}, validationErrors: {}
};

const withUpdateMember = withRequest(MembersService.updateMember, {
  name: 'updateMember',
  init: false,
  options: ({ formData, setFormData, member, setAlert, onClose }) => {
    const changes = _pickBy(formData, (value, key) => {
      const previousVal = formData.previousValues[key];
      return previousVal && !_isEqual(previousVal, value);
    });
    return {
      variables: {
        id: member.id,
        ...changes,
        ...(
          changes.buildingsOptions
            ? {
              buildingIds: _map(_pickBy(changes.buildingsOptions, { checked: true }), b => b.value)
            }
            : null
        )
      },
      onRequestFinished: () => {
        Tracker.hit('MemberFormModal - Saved Success');
        setAlert({
          alertMsg: 'Usurio alterado com sucesso',
          alertType: 'success',
          showAlert: true
        });
        onClose();
      },
      onRequestError: error => {
        Tracker.hit('MemberFormModal - Save Error');
        setAlert({
          alertMsg: 'Erro ao alterar este usurio',
          alertType: 'error',
          showAlert: true
        });

        const errors = _mapValues(_get(error, 'errors', {}), (err) => err[0]);
        setFormData({ validationErrors: errors });
      }
    };
  }
});

const withBlockMember = withRequest(MembersService.blockMember, {
  name: 'blockMember',
  init: false,
  options: ({ member, onClose, setAlert }) => ({
    variables: _get(member, 'id'),
    onRequestFinished: () => {
      Tracker.hit('MemberFormModal - Block Success');
      setAlert({
        alertMsg: 'Usurio bloqueado com sucesso',
        alertType: 'success',
        showAlert: true
      });
      onClose();
    },
    onRequestError: () => {
      Tracker.hit('MemberFormModal - Block Fail');
      setAlert({
        alertMsg: 'Erro ao bloquear este usurio',
        alertType: 'error',
        showAlert: true
      });
    }
  })
});
export default compose(
  withStyles(styles, { withTheme: true }),
  withState('formData', 'setFormDataRaw', initialFormData),
  withHandlers({
    setFormData: ({ setFormDataRaw, formData }) => (attrs) => {
      setFormDataRaw({ ...formData, ...attrs });
    }
  }),
  withBlockMember,
  withUpdateMember
)(MemberFormModal);
