import moment from 'moment';
import _get from 'lodash/get';
import Api from '../utils/api';

const SpaceBookings = {
  getBookings({ spaceId, from, to }) {
    return Api.get(`/spaces/${spaceId}/space_bookings?from=${moment(from).toISOString()}&to=${moment(to).toISOString()}`);
  },
  createBookings(bookingData) {
    const body = {
      title: _get(bookingData, 'title'),
      space_id: _get(bookingData, 'spaceId'),
      booking_date: _get(bookingData, 'bookingDate'),
      periods: _get(bookingData, 'periods', [])
    };
    return Api.post('/space_bookings', body);
  },
  cancelBooking(bookingId) {
    return Api.put(`/space_bookings/${bookingId}/cancel`);
  }
};

export default SpaceBookings;
