/* global window */
import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router';
import { Link } from 'react-router-dom';
import { compose, withState } from 'recompose';
import _get from 'lodash/get';
import _size from 'lodash/size';
import { withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import Hidden from '@material-ui/core/Hidden';
import Divider from '@material-ui/core/Divider';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import AvatarImage from '../AvatarImage';
import AlertBox from '../AlertBox';
import Session from '../../utils/session';
import Tracker from '../../utils/tracker';
import styles from './styles';
import config from '../../config';
import Routes from '../../routes';

const hasSpaces = _get(Session.getUserData(), 'member.has_spaces', '');
const hasVisitors = _get(Session.getUserData(), 'member.has_visitors', '');
let tabMenu = Object.values(Routes).filter((r) => !r.hideMenu && !r.settingsMenu);
if (!hasVisitors) {
  tabMenu = tabMenu.filter((r) => r.label !== 'Visitantes');
}
if (!hasSpaces) {
  tabMenu = tabMenu.filter((r) => r.label !== 'Reservas');
}

class Header extends PureComponent {
  state = {
    userName: '',
    userDataError: false,
    sideMenuOpen: false,
    profileMenuAnchor: null
  }

  componentDidMount() {
    this.loadData();
  }

  loadData = () => {
    const enterpriseUser = Session.getUserData();
    const memberName = _get(enterpriseUser, 'member.name', '');
    const memberLastname = _get(enterpriseUser, 'member.lastname', '');
    this.setState({ userName: `${memberName} ${memberLastname}` });
  }

  onLogout = () => {
    this.setState({ profileMenuAnchor: null }, () => {
      if (window.confirm('Voc deseja sair?')) {
        const { history } = this.props;
        Tracker.hit('Logout');
        Session.clear();
        history.push('/');
      }
    });
  }

  onSettings = () => {
    this.setState({ profileMenuAnchor: null }, () => {
      const { history } = this.props;
      Tracker.hit('Settings');
      history.push('/configuracoes/meu-perfil');
    });
  }

  onToggleSideMenu = () => {
    const { sideMenuOpen } = this.state;
    this.setState({ sideMenuOpen: !sideMenuOpen });
  }

  onChangePage = (e, tabIndex) => {
    const { history } = this.props;
    history.push(tabMenu[tabIndex].path);
  }

  onToggleProfileMenu = e => this.setState({ profileMenuAnchor: e.currentTarget });

  renderErrorAlertBox = (error) => {
    const { history } = this.props;
    if (Session.isAuthorized(error)) {
      return (
        <AlertBox
          type="error"
          message="Erro ao carregar dados do servidor"
          actionButton={<b>Recarregar</b>}
          onClickAction={() => window.location.reload()}
        />
      );
    }
    return (
      <AlertBox
        type="error"
        message="Sua sesso expirou, faa login novamente"
        actionButton={<b>Fazer login</b>}
        onClickAction={() => {
          Session.clear();
          history.push('/');
        }}
      />
    );
  }

  renderWebappUpdateAlertBox = () => {
    return (
      <AlertBox
        type="success"
        message="Uma nova atualizao foi instalada."
        actionButton={<b>Continuar</b>}
        onClickAction={() => window.location.reload()}
      />
    );
  }

  renderAlerts = () => {
    const { alertBox, webappUpdated, error } = this.props;
    const { userDataError } = this.state;
    const err = error || userDataError;
    if (alertBox) {
      return alertBox;
    }
    if (webappUpdated) {
      return this.renderWebappUpdateAlertBox();
    }
    if (err) {
      return this.renderErrorAlertBox(err);
    }
    return null;
  }

  renderSideMenu(isSmallScreen) {
    const { classes, history } = this.props;
    const { sideMenuOpen } = this.state;
    return (
      <Drawer
        classes={{ paper: classes.drawer }}
        variant={isSmallScreen ? 'persistent' : 'permanent'}
        anchor="left"
        open={sideMenuOpen}
        ModalProps={{ keepMounted: !isSmallScreen }}
      >
        <div className={classes.headerLogoContainer}>
          {
            isSmallScreen &&
              <div className={classes.closeMenuIcon}>
                <IconButton size="medium" onClick={this.onToggleSideMenu}>
                  <ChevronLeftIcon />
                </IconButton>
              </div>
          }
          <div className={classes.logoContainer}>
            <img
              src={require('../../assets/images/logo-header.png')}
              alt={config.appName}
              className={classes.logo}
            />
          </div>
        </div>
        <Divider className={classes.divider} />
        <List>
          {tabMenu.map((menu, tabKey) => {
            const subMenus = Object.values(menu.submenus || {});
            const hasSubmenu = Boolean(_size(subMenus));
            let listItemProps = {};
            if (!hasSubmenu) {
              listItemProps = {
                component: Link,
                to: menu.path.replace(/:[\w*]+/g, ''),
                selected: menu.path.includes(history.location.pathname)
              };
            }
            return (
              <div key={tabKey.toString()}>
                <ListItem
                  button={!hasSubmenu}
                  key={menu.path}
                  {...listItemProps}
                >
                  {
                    menu.icon &&
                      <ListItemIcon className={classes.listItemIcon}>
                        {menu.icon}
                      </ListItemIcon>
                  }
                  <ListItemText
                    className={classes.listItemText}
                    disableTypography
                    primary={menu.label}
                  />
                </ListItem>
                {
                  hasSubmenu &&
                    <List className={classes.submenuList}>
                      {
                        subMenus.map((submenu) => {
                          const subMenuPath = `${menu.path}${submenu.path}`;
                          return (
                            <ListItem
                              button
                              className={classes.submenuItem}
                              key={subMenuPath}
                              component={Link}
                              to={subMenuPath.replace(/:[\w*]+/g, '')}
                              selected={subMenuPath === history.location.pathname}
                            >
                              {
                                submenu.icon &&
                                  <ListItemIcon className={classes.listItemIcon}>
                                    {submenu.icon}
                                  </ListItemIcon>
                              }
                              <ListItemText
                                className={classes.listItemText}
                                disableTypography
                                primary={submenu.label}
                              />
                            </ListItem>
                          );
                        })
                      }
                    </List>
                }
              </div>
            );
          })}
        </List>
      </Drawer>
    );
  }

  render() {
    const { classes } = this.props;
    const { userName, profileMenuAnchor } = this.state;
    return (
      <React.Fragment>
        <div className={classes.root}>
          <AppBar position="fixed" className={classes.appBar}>
            <Toolbar>
              <IconButton
                aria-label="open drawer"
                onClick={this.onToggleSideMenu}
                edge="start"
              >
                <MenuIcon />
              </IconButton>
              <div className={classes.profileMenuContainer}>
                <AvatarImage name={userName} photoUrl="" />
                <span className={classes.userName} onClick={this.onToggleProfileMenu}>
                  {userName}
                </span>
                <IconButton size="medium" onClick={this.onToggleProfileMenu}>
                  <KeyboardArrowDownIcon />
                </IconButton>
                <Menu
                  keepMounted
                  PaperProps={{ style: { marginTop: 35 } }}
                  anchorEl={profileMenuAnchor}
                  open={Boolean(profileMenuAnchor)}
                  onClose={() => this.setState({ profileMenuAnchor: null })}
                  anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
                  transformOrigin={{ vertical: 'top', horizontal: 'right' }}
                >
                  <MenuItem onClick={this.onSettings}>Configuraes</MenuItem>
                  <MenuItem onClick={this.onLogout}>Sair</MenuItem>
                </Menu>
              </div>
            </Toolbar>
          </AppBar>
          <Hidden smDown>
            {this.renderSideMenu(false)}
          </Hidden>
          <Hidden smUp>
            {this.renderSideMenu(true)}
          </Hidden>
          {this.renderAlerts()}
        </div>
      </React.Fragment>
    );
  }
}

Header.propTypes = {
  history: PropTypes.object,
  alertBox: PropTypes.any,
  error: PropTypes.any,
  webappUpdated: PropTypes.bool,
  classes: PropTypes.object
};

export default compose(
  withStyles(styles, { withTheme: true }),
  withRouter,
  withState('webappUpdated', 'setWebappUpdated', false)
)(Header);
