/* global window */
import React, { memo } from 'react';
import PropTypes from 'prop-types';
import _get from 'lodash/get';
import _set from 'lodash/set';
import moment from 'moment';
import { withRouter } from 'react-router';
import { compose, withState } from 'recompose';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Chip from '@material-ui/core/Chip';
import Typography from '@material-ui/core/Typography';
import Skeleton from '@material-ui/lab/Skeleton';
import AvatarImage from '../../../components/AvatarImage';
import TicketModal from '../TicketModal';
import styles from './styles';

const renderBadge = (deadline) => {
  if (deadline) {
    const deadlineLabel = moment(deadline).format('MMM D');
    const isToday = moment(deadline).startOf('day').isSame(moment().startOf('day'), 'day');
    const isTomorrow = moment(deadline).startOf('day').diff(moment().startOf('day'), 'days') === 1;
    const isExpired = moment(deadline).startOf('day').isBefore(moment().startOf('day'), 'day');
    if (isToday) {
      return <Chip label={deadlineLabel} size="small" style={styles.cardChipToday} />;
    }
    if (isTomorrow) {
      return <Chip label={deadlineLabel} size="small" style={styles.cardChipTomorrow} />;
    }
    if (isExpired) {
      return <Chip label={deadlineLabel} size="small" style={styles.cardChipExpired} />;
    }
    return <Chip label={deadlineLabel} size="small" style={styles.cardChipFarFromExpiration} />;
  }
  return null;
};

const TicketCard = memo(({
  ticket, openModal, setOpenModal, onCloseModal,
  categories, assignees, match, loading, hasTicketToOpenModal
}) => {
  const canOpenModal = () => {
    if (openModal) {
      return true;
    }
    if (hasTicketToOpenModal) {
      return false;
    }
    if (_get(match, 'params.id')) {
      return _get(ticket, 'id') === _get(match, 'params.id');
    }
    return false;
  };
  const onClickCard = () => {
    if (!loading) {
      setOpenModal(true);
      window.history.pushState('', '', `/#/tickets/${_get(ticket, 'id')}`);
    }
  };
  const onCloseCard = (err, alertMsg) => {
    _set(match, 'params.id', null);
    setOpenModal(false);
    onCloseModal(err, alertMsg);
  };
  const requesterName = `${_get(ticket, 'requester.name')} ${_get(ticket, 'requester.lastname', '')}`;
  const assigneeName = `${_get(ticket, 'assignee.name')} ${_get(ticket, 'assignee.lastname', '')}`;
  const createdAtLabel = _get(ticket, 'created_at') ? `${moment(ticket.created_at).fromNow()} por ` : '';
  return (
    <React.Fragment>
      <Card style={styles.card} onClick={onClickCard}>
        <CardContent style={styles.cardContent}>
          {!loading && renderBadge(_get(ticket, 'deadline'))}
          <Typography variant="subtitle1" style={styles.cardTitle}>
            {
              loading ?
                <Skeleton animation="wave" />
                :
                <b>{_get(ticket, 'title', '')}</b>
            }
          </Typography>
          {
            loading ?
              <Skeleton animation="wave" width={150} />
              :
              <Typography variant="body2" color="textSecondary" component="small">
                {createdAtLabel}
                <b>{requesterName}</b>
              </Typography>
          }
          <div style={styles.cardAction}>
            <div style={styles.cardAvatar}>
              {
                loading ?
                  <Skeleton animation="wave" width={100} />
                  :
                  _get(ticket, 'assignee', '') &&
                    <React.Fragment>
                      <AvatarImage name={assigneeName} />
                      <Typography variant="subtitle1" style={styles.cardAvatarName}>
                        <small>{assigneeName}</small>
                      </Typography>
                    </React.Fragment>
              }
            </div>
            <Typography variant="body2" color="textSecondary" component="small">
              {
                loading ?
                  <Skeleton animation="wave" width={50} />
                  :
                  _get(ticket, 'category.title', '')
              }
            </Typography>
          </div>
        </CardContent>
      </Card>
      <TicketModal
        open={canOpenModal()}
        ticket={ticket}
        categories={categories}
        assignees={assignees}
        onClose={onCloseCard}
      />
    </React.Fragment>
  );
});

TicketCard.propTypes = {
  match: PropTypes.object,
  ticket: PropTypes.object,
  loading: PropTypes.bool,
  categories: PropTypes.array,
  assignees: PropTypes.array,
  openModal: PropTypes.bool,
  setOpenModal: PropTypes.func,
  onCloseModal: PropTypes.func,
  hasTicketToOpenModal: PropTypes.bool
};

export default compose(
  withRouter,
  withState('openModal', 'setOpenModal', false)
)(TicketCard);
