import React, { memo } from 'react';
import PropTypes from 'prop-types';
import _get from 'lodash/get';
import _size from 'lodash/size';
import _sortBy from 'lodash/sortBy';
import _isEmpty from 'lodash/isEmpty';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import ErrorIcon from '@material-ui/icons/Error';
import FormControl from '@material-ui/core/FormControl';
import TextField from '@material-ui/core/TextField';
import { compose, withState } from 'recompose';
import withRequest from '../../utils/with-request';
import Session from '../../utils/session';
import SpacesService from '../../services/spaces';
import SpacesCard from './SpacesCard';
import Header from '../../components/Header';
import AlertBox from '../../components/AlertBox';
import Loading from '../../components/Loading';
import Tracker from '../../utils/tracker';
import styles from './styles';

const firstBuildingId = _get(Session.getUserData(), 'buildings[0].id');

const Spaces = memo(({
  classes, alert, setAlert, spacesData, spacesError, spacesFetch,
  spacesLoading, setBuildingId, buildingId
}) => {
  const { showAlert, alertMsg, alertType } = alert;
  const onClearAlert = () => setAlert({ alertMsg: '', alertType: '', showAlert: false });
  let alertBox = null;
  if (showAlert) {
    alertBox = <AlertBox message={alertMsg} type={alertType} onClose={onClearAlert} />;
  }
  const onSelectBuilding = async (e) => {
    setBuildingId(e.target.value);
    spacesFetch(true);
    const trackProps = {
      select_building: e.target.value
    };
    Tracker.hit('Spaces - FilteredByBuilding', trackProps);
  };
  const spaces = _get(spacesData, 'spaces', []);
  const buildings = _sortBy(_get(Session.getUserData(), 'buildings', []), ['name']);
  return (
    <div className={classes.root}>
      <Header alertBox={alertBox} error={spacesError} />
      <section>
        <div className={classes.container}>
          <div className={classes.headerView}>
            <aside>
              <Typography variant="h5">
                <b>
                  {_size(spaces) ? `Reserva de espaos (${_size(spaces)})` : 'Reserva de espaos'}
                </b>
              </Typography>
            </aside>
          </div>
          <Grid container spacing={1}>
            <Grid item xs={12} sm={6} md={3}>
              <FormControl fullWidth>
                <TextField
                  select
                  label="Prdio"
                  className={classes.selectBox}
                  value={buildingId}
                  SelectProps={{ native: true }}
                  InputLabelProps={{ shrink: true }}
                  onChange={onSelectBuilding}
                >
                  {buildings.map(b => <option key={b.id} value={b.id}>{b.name}</option>)}
                </TextField>
              </FormControl>
            </Grid>
          </Grid>
        </div>
        <div className={classes.container}>
          <Grid container spacing={1}>
            {
              spacesLoading ?
                <Loading />
                :
                (
                  _isEmpty(spaces) ?
                    <div className={classes.centerHeaderView}>
                      <ErrorIcon className={classes.emptyHeaderIcon} />
                      <b className={classes.emptyHeaderTitle}>
                        Este prdio no possui espao para reserva
                      </b>
                    </div>
                    :
                    spaces.map(space => <SpacesCard key={space.id} space={space} />)
                )
            }
          </Grid>
        </div>
      </section>
    </div>
  );
});

Spaces.propTypes = {
  classes: PropTypes.object,
  alert: PropTypes.object,
  setAlert: PropTypes.func,
  buildingId: PropTypes.string,
  setBuildingId: PropTypes.func,
  spacesLoading: PropTypes.bool,
  spacesError: PropTypes.object,
  spacesData: PropTypes.object,
  spacesFetch: PropTypes.func
};

const withSpaces = withRequest(SpacesService.getSpaces, {
  name: 'spaces',
  options: ({ buildingId }) => ({ variables: buildingId })
});

export default compose(
  withStyles(styles, { withTheme: true }),
  withState('buildingId', 'setBuildingId', firstBuildingId),
  withState('alert', 'setAlert', {
    alertMsg: '',
    alertType: '',
    showAlert: false
  }),
  withSpaces
)(Spaces);
