import React, { memo } from 'react';
import PropTypes from 'prop-types';
import Snackbar from '@material-ui/core/Snackbar';
import SnackbarContent from '@material-ui/core/SnackbarContent';
import CloseIcon from '@material-ui/icons/Close';
import IconButton from '@material-ui/core/IconButton';
import Button from '@material-ui/core/Button';
import { withStyles } from '@material-ui/core/styles';
import styles from './styles';

const AlertBox = memo(({ message, type, actionButton, onClickAction, onClose, classes }) => {
  const actions = [];
  if (actionButton) {
    actions.push(
      <Button
        key="action"
        color="inherit"
        variant="outlined"
        className={classes[type || 'info'] || classes.info}
        onClick={onClickAction}
      >
        {actionButton}
      </Button>
    );
  }
  if (onClose) {
    actions.push(
      <IconButton key="action" color="inherit" onClick={onClose}>
        <CloseIcon className={classes.closeIcon} />
      </IconButton>
    );
  }
  return (
    <Snackbar
      anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
      open
      autoHideDuration={onClose ? 3000 : null}
      onClose={() => onClose && onClose()}
    >
      <SnackbarContent
        className={classes[type || 'info'] || classes.info}
        message={
          <span className={classes.message}>
            {message}
          </span>
        }
        action={actions}
      />
    </Snackbar>
  );
});

AlertBox.propTypes = {
  message: PropTypes.string,
  type: PropTypes.string,
  actionButton: PropTypes.any,
  onClose: PropTypes.func,
  onClickAction: PropTypes.func,
  classes: PropTypes.object
};

export default withStyles(styles, { withTheme: true })(AlertBox);
