import axios from 'axios';
import _get from 'lodash/get';
import Session from './session';
import config from '../config';

const getErrorMsg = (err) => {
  let errorData = _get(err, 'response.data');
  if (!errorData) {
    errorData = { error: _get(err, 'message') };
  }
  return errorData;
};

const Api = {
  request(method = 'get', endpoint, body, params) {
    return new Promise(async (resolve, reject) => {
      try {
        const bearerToken = Session.getBearerToken();
        const agent = axios.create({
          baseURL: `${config.apiURL}/${config.appNamespace}`,
          timeout: config.apiRequestTimeout,
          headers: {
            'Content-Type': 'application/json',
            Authorization: bearerToken
          },
          responseType: 'json'
        });
        const { data } = await agent[method](endpoint, body, params);
        return resolve(data);
      } catch (err) {
        return reject(getErrorMsg(err));
      }
    });
  },
  get(endpoint, params) {
    return this.request('get', endpoint, { params });
  },
  post(endpoint, body, params) {
    return this.request('post', endpoint, body, params);
  },
  put(endpoint, body, params) {
    return this.request('put', endpoint, body, params);
  },
  delete(endpoint, body, params) {
    return this.request('delete', endpoint, body, params);
  }
};

export default Api;
