import React, { memo } from 'react';
import PropTypes from 'prop-types';
import styles from '../styles';

const CardContainer = memo(({ provided = {}, children }) => (
  <a
    style={styles.card}
    ref={provided.innerRef}
    {...provided.draggableProps}
    {...provided.dragHandleProps}
  >
    {children}
  </a>
));

CardContainer.propTypes = {
  children: PropTypes.any,
  provided: PropTypes.any
};

const Card = memo(({ provided, renderCard, row, cardWrapperStyle, disableDragDrop }) => {
  return (
    <CardContainer provided={disableDragDrop ? {} : provided}>
      <div style={{ ...styles.cardContent, ...cardWrapperStyle }}>
        {renderCard(row)}
      </div>
    </CardContainer>
  );
});

Card.propTypes = {
  disableDragDrop: PropTypes.bool,
  provided: PropTypes.object,
  renderCard: PropTypes.func,
  row: PropTypes.object,
  cardWrapperStyle: PropTypes.object
};

export default Card;
