import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import _size from 'lodash/size';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';
import styles from './styles';
import config from '../../config';
import PasswordService from '../../services/password';
import AlertBox from '../../components/AlertBox';
import Loading from '../../components/Loading';

const MIN_PASSWORD = 6;

class CreatePassword extends PureComponent {
  state = {
    memberId: '',
    recoveryCode: '',
    password: '',
    confirmPassword: '',
    msg: '',
    msgType: '',
    hideForm: true,
    loading: true,
    submit: false
  }

  componentDidMount() {
    const { location } = this.props;
    this.setState({ submit: true, loading: true, memberId: '', recoveryCode: '' }, async () => {
      try {
        const query = new URLSearchParams(location.search);
        const id = query.get('id');
        const code = query.get('code');
        if (id && code) {
          await PasswordService.verifyIfCanResetPassword(id, code);
          this.setState({
            submit: false,
            loading: false,
            hideForm: false,
            memberId: id,
            recoveryCode: code
          });
          return true;
        }
        throw new Error('Invalid URL query');
      } catch {
        this.setState({ hideForm: true, loading: false });
      }
      return true;
    });
  }

  onSubmit = (e) => {
    e.preventDefault();
    const { history } = this.props;
    const { confirmPassword, password, memberId, recoveryCode } = this.state;
    this.setState({ submit: true }, async () => {
      try {
        if (confirmPassword && password && confirmPassword === password) {
          if (_size(password) >= MIN_PASSWORD) {
            await PasswordService.changePassword(memberId, recoveryCode, password);
            this.setState({
              msg: 'Senha atualizada, redirecionando para tela de login...',
              msgType: 'success',
              memberId: '',
              recoveryCode: '',
              confirmPassword: '',
              password: ''
            }, () => {
              setTimeout(() => {
                history.replace('/');
              }, 2500);
            });
            return true;
          }
          this.sendAlert(`Informe uma senha com mnimo de ${MIN_PASSWORD} caracteres.`, 'warning');
          return true;
        }
        this.sendAlert('As senhas esto em branco ou diferentes.', 'warning');
      } catch {
        this.sendAlert('No foi possvel alterar senha.', 'warning');
      }
      return true;
    });
  }

  sendAlert(msg, msgType) {
    this.setState({ submit: false, msg, msgType });
  }

  clearAlert() {
    this.setState({ submit: false, msg: '', msgType: '' });
  }

  render() {
    const { confirmPassword, password, submit, loading, hideForm, msg, msgType } = this.state;
    return (
      <div style={styles.container}>
        <div style={styles.content}>
          {
            msg && msgType &&
              <AlertBox message={msg} type={msgType} onClose={() => this.clearAlert} />
          }
          {
            loading ?
              <Loading />
              :
              <React.Fragment>
                <img
                  src={require('../../assets/images/logo.png')}
                  width={150}
                  alt={config.appName}
                  style={styles.logo}
                />
                <Typography component="h1" variant="h5">
                  {hideForm ? 'Link invlido para definir senha' : 'Definir senha'}
                </Typography>
                {
                  !hideForm &&
                    <form onSubmit={this.onSubmit} style={styles.form}>
                      <TextField
                        variant="outlined"
                        margin="normal"
                        fullWidth
                        label="Senha"
                        type="password"
                        disabled={submit}
                        value={password}
                        InputLabelProps={{ shrink: true }}
                        onChange={e => this.setState({ password: e.target.value })}
                      />
                      <TextField
                        variant="outlined"
                        margin="normal"
                        fullWidth
                        label="Confirmar senha"
                        type="password"
                        disabled={submit}
                        value={confirmPassword}
                        InputLabelProps={{ shrink: true }}
                        onChange={e => this.setState({ confirmPassword: e.target.value })}
                      />
                      <Button
                        disabled={submit}
                        fullWidth
                        variant="contained"
                        color="primary"
                        size="large"
                        style={styles.button}
                        type="submit"
                      >
                        {submit ? 'Carregando...' : 'Entrar'}
                      </Button>
                    </form>
                }
              </React.Fragment>
          }
          <p><Link to="/">Voltar para home</Link></p>
        </div>
      </div>
    );
  }
}

CreatePassword.propTypes = {
  location: PropTypes.object,
  history: PropTypes.object
};

export default CreatePassword;
