import Api from '../utils/api';

const TicketComments = {
  getComments(ticketId) {
    if (ticketId) {
      return Api.get(`/tickets/${ticketId}/ticket_comments`);
    }
    return null;
  },
  sendComment({ id, content }) {
    return Api.post(`/tickets/${id}/ticket_comments`, { content });
  }
};

export default TicketComments;
