import React, { memo } from 'react';
import Avatar from '@material-ui/core/Avatar';
import { withStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import _first from 'lodash/first';
import styles from './styles';

const getColorName = (value = 'a') => {
  const colorMap = {
    a: 'gold',
    b: 'gold',
    c: 'gold',
    d: 'purple',
    e: 'purple',
    f: 'purple',
    g: 'black',
    h: 'black',
    i: 'black',
    j: 'blue',
    k: 'blue',
    l: 'blue',
    m: 'green',
    n: 'green',
    o: 'green',
    p: 'brown',
    q: 'brown',
    r: 'brown',
    s: 'pink',
    t: 'pink',
    u: 'pink',
    v: 'red',
    w: 'red',
    y: 'red',
    x: 'lime',
    z: 'lime'
  };
  return colorMap[value.toLowerCase()];
};

const AvatarImage = memo(({ classes, name, photoUrl }) => {
  const firstChar = _first(name) || '';
  return (
    <Avatar
      alt={name}
      src={photoUrl}
      className={classes[getColorName(firstChar)]}
    >
      {firstChar.toUpperCase()}
    </Avatar>
  );
});

AvatarImage.propTypes = {
  classes: PropTypes.object,
  name: PropTypes.string,
  photoUrl: PropTypes.string
};

export default withStyles(styles, { withTheme: true })(AvatarImage);
