import React, { memo } from 'react';
import PropTypes from 'prop-types';
import _size from 'lodash/size';
import _get from 'lodash/get';
import List from '../List';
import styles from '../styles';

const Column = memo(({
  index, column, renderCard, columnStyle,
  columnWrapperStyle, cardWrapperStyle,
  columnTitleStyle, columnHeaderStyle,
  disableDragDrop, loading
}) => (
  <div style={{ ...styles.column, ...columnWrapperStyle }}>
    <header style={{ ...styles.columnHeader, ...columnHeaderStyle }}>
      <h4
        style={{
          ...styles.columnTitle,
          ...columnTitleStyle,
          ...(loading ? styles.columnTitleLoading : {})
        }}
      >
        {`${_get(column, 'title')} (${_size(_get(column, 'rows'))})`}
      </h4>
    </header>
    <List
      key={index}
      columnStyle={columnStyle}
      column={column}
      loading={loading}
      listStatus={_get(column, 'status')}
      renderCard={renderCard}
      cardWrapperStyle={cardWrapperStyle}
      disableDragDrop={disableDragDrop}
    />
  </div>
));

Column.propTypes = {
  index: PropTypes.number,
  renderCard: PropTypes.func,
  loading: PropTypes.bool,
  column: PropTypes.object,
  columnWrapperStyle: PropTypes.object,
  columnStyle: PropTypes.object,
  columnTitleStyle: PropTypes.object,
  columnHeaderStyle: PropTypes.object,
  cardWrapperStyle: PropTypes.object,
  disableDragDrop: PropTypes.bool
};

export default Column;
