import Api from '../utils/api';

const Visitors = {
  getVisitors({ page = 0, perPage = 20, month = 11, year, buildingId }) {
    if (buildingId) {
      return Api.get('/visitors', { page, per_page: perPage, month, year, building_id: buildingId });
    }
    return null;
  },
  createVisitors({
    visitorData, companyId, visitorCompany,
    visitTime, visitDate, hostMemberId, buildingId
  }) {
    const repeatedData = {
      company_id: companyId,
      visitor_company: visitorCompany,
      visit_hour: visitTime,
      visit_date: visitDate,
      host_member_id: hostMemberId,
      building_id: buildingId
    };
    const visitors = visitorData.map(v => ({ ...v, ...repeatedData }));
    return Api.post('/visitors', { visitors });
  },
  deleteVisitor(visitorId) {
    return Api.put(`/visitors/${visitorId}/cancel`);
  }
};

export default Visitors;
