/* global localStorage */
const Store = {
  save(key, value) {
    return localStorage.setItem(key, JSON.stringify(value));
  },
  get(key) {
    try {
      const value = localStorage.getItem(key);
      const data = JSON.parse(value);
      return data;
    } catch (err) {
      console.log(err);
      return {};
    }
  },
  delete(key) {
    return localStorage.removeItem(key);
  }
};

export default Store;
