/* global window */
import React from 'react';
import PropTypes from 'prop-types';
import _get from 'lodash/get';
import _sortBy from 'lodash/sortBy';
import _size from 'lodash/size';
import _isEmpty from 'lodash/isEmpty';
import { compose, lifecycle, withState } from 'recompose';
import { Link } from 'react-router-dom';
import { withRouter } from 'react-router';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import FormControl from '@material-ui/core/FormControl';
import ErrorIcon from '@material-ui/icons/Error';
import TicketBoard from './TicketBoard';
import Header from '../../components/Header';
import AlertBox from '../../components/AlertBox';
import TicketsService from '../../services/tickets';
import TicketCategoriesService from '../../services/ticket-categories';
import Session from '../../utils/session';
import Screen from '../../utils/screen';
import Tracker from '../../utils/tracker';
import withRequest from '../../utils/with-request';
import UserRolesEnum from '../../enums/user-roles';
import styles from './styles';

const { MEMBER_ROLE } = UserRolesEnum;

const Tickets = ({
  alert, setAlert, classes, assigneeId, setAssigneeId, code, setCode,
  buildingId, setBuildingId, ticketsFetch, ticketsData, ticketByCodeFetch,
  ticketsError, ticketsLoading, ticketCategoriesData, ticketLoading,
  ticketCategoriesError, ticketCategoriesLoading, categoryId, setCategoryId,
  ticketAssigneesData, ticketAssigneesError, ticketAssigneesLoading,
  ticketToOpenModal, setTicketToOpenModal, ticketByCodeLoading, moveTicketFetch
}) => {
  const enterpriseUser = Session.getUserData();
  const hasTicketSupport = _get(enterpriseUser, 'member.company.has_ticket_support');
  if (hasTicketSupport) {
    const buildings = _sortBy(_get(enterpriseUser, 'buildings', []), ['name']);
    const currentRole = _get(enterpriseUser, 'member.role', MEMBER_ROLE);
    const lockBoard = currentRole === MEMBER_ROLE;
    const tickets = _get(ticketsData, 'tickets', []);
    const categories = _sortBy(_get(ticketCategoriesData, 'ticket_categories', []), ['title']);
    const assignees = _sortBy(_get(ticketAssigneesData, 'members', []), ['name', 'lastname']);
    const loading = (
      ticketsLoading || ticketCategoriesLoading ||
      ticketAssigneesLoading || ticketByCodeLoading || ticketLoading
    );
    const isDisabled = loading;
    const error = (
      ticketsError || ticketCategoriesError || ticketAssigneesError
    );
    const onDragCard = async (params) => {
      setTicketToOpenModal(null);
      await moveTicketFetch(false, '', null, params);
      ticketsFetch(false);
    };
    const onCloseCardModal = (err, alertMsg, done) => {
      setTicketToOpenModal(null);
      ticketsFetch(false);
      window.history.pushState('', '', '/#/tickets');
      if (alertMsg) {
        setAlert({ alertMsg, alertType: err ? 'error' : 'success', showAlert: true });
      }
      return done && done();
    };
    const onChangeTicketCode = (e) => {
      const newCode = e.target.value.replace(/[\D]+/g, '');
      if (code !== newCode) {
        setCode(newCode);
      }
    };
    const onMobileRequestTicketByCode = () => {
      if (Screen.isMobile()) {
        const trackProps = { select_code: code };
        Tracker.hit('Tickets - FilteredByCode', trackProps);
        setTicketToOpenModal(null);
        ticketByCodeFetch(true);
      }
    };
    const onRequestTicketByCode = (e) => {
      if (e.keyCode === 13) {
        const trackProps = { select_code: code };
        Tracker.hit('Tickets - FilteredByCode', trackProps);
        setTicketToOpenModal(null);
        ticketByCodeFetch(true);
      }
    };
    const onSelectAssignee = (e) => {
      setAssigneeId(e.target.value);
      const trackProps = { select_assignee: e.target.value };
      Tracker.hit('Tickets - FilteredByAssignee', trackProps);
      ticketsFetch(true);
    };
    const onSelectCategory = (e) => {
      setCategoryId(e.target.value);
      const trackProps = { select_category: e.target.value };
      Tracker.hit('Tickets - FilteredByCategory', trackProps);
      ticketsFetch(true);
    };
    const onSelectBuilding = (e) => {
      setBuildingId(e.target.value);
      const trackProps = { select_building: e.target.value };
      Tracker.hit('Tickets - FilteredByBuilding', trackProps);
      ticketsFetch(true);
    };
    const { showAlert, alertMsg, alertType } = alert;
    const onClearAlert = () => setAlert({ alertMsg: '', alertType: '', showAlert: false });
    let alertBox = null;
    if (showAlert) {
      alertBox = <AlertBox message={alertMsg} type={alertType} onClose={onClearAlert} />;
    }
    return (
      <div className={classes.root}>
        <Header alertBox={alertBox} error={error} />
        <section>
          <div className={classes.container}>
            <div className={classes.headerView}>
              <aside>
                <Typography variant="h5">
                  <b>{_size(tickets) ? `Tickets (${_size(tickets)})` : 'Tickets'}</b>
                </Typography>
                <br />
                <Grid container spacing={2}>
                  <Grid item xs={7} sm={2}>
                    <FormControl fullWidth>
                      <TextField
                        label="Ticket"
                        placeholder="Por n"
                        disabled={isDisabled}
                        value={code}
                        type="tel"
                        InputLabelProps={{ shrink: true }}
                        onKeyDown={onRequestTicketByCode}
                        onBlur={onMobileRequestTicketByCode}
                        onChange={onChangeTicketCode}
                      />
                    </FormControl>
                  </Grid>
                  <Grid item xs={7} sm={3}>
                    <FormControl fullWidth>
                      <TextField
                        select
                        label="Prdio"
                        disabled={isDisabled}
                        value={buildingId}
                        InputLabelProps={{ shrink: true }}
                        SelectProps={{ native: true }}
                        onChange={onSelectBuilding}
                      >
                        {buildings.map(b => (
                          <option key={b.id} value={b.id}>
                            {b.name}
                          </option>
                        ))}
                      </TextField>
                    </FormControl>
                  </Grid>
                  <Grid item xs={7} sm={3}>
                    <FormControl fullWidth>
                      <TextField
                        select
                        label="Responsvel"
                        disabled={isDisabled}
                        value={assigneeId}
                        InputLabelProps={{ shrink: true }}
                        SelectProps={{ native: true }}
                        onChange={onSelectAssignee}
                      >
                        <option value="">Todos tickets</option>
                        {assignees.map(a => (
                          <option key={a.id} value={a.id}>
                            {`${a.name} ${a.lastname}`}
                          </option>
                        ))}
                      </TextField>
                    </FormControl>
                  </Grid>
                  <Grid item xs={7} sm={3}>
                    <FormControl fullWidth>
                      <TextField
                        select
                        label="Categoria"
                        disabled={isDisabled}
                        value={categoryId}
                        InputLabelProps={{ shrink: true }}
                        SelectProps={{ native: true }}
                        onChange={onSelectCategory}
                      >
                        <option value="">Todas categorias</option>
                        {categories.map(b => (
                          <option key={b.id} value={b.id}>
                            {b.title}
                          </option>
                        ))}
                      </TextField>
                    </FormControl>
                  </Grid>
                </Grid>
              </aside>
              <aside>
                <Button
                  variant="contained"
                  component={Link}
                  to="/tickets/novo"
                  color="primary"
                  size="large"
                >
                  Novo ticket
                </Button>
              </aside>
            </div>
          </div>
          <div className={classes.container}>
            <Grid container>
              <Grid item xs={12}>
                <TicketBoard
                  lockBoard={lockBoard}
                  tickets={tickets}
                  ticketToOpenModal={ticketToOpenModal}
                  categories={categories}
                  assignees={assignees}
                  loading={loading}
                  onDragCard={onDragCard}
                  onCloseCardModal={onCloseCardModal}
                />
              </Grid>
            </Grid>
          </div>
        </section>
      </div>
    );
  }
  return (
    <div className={classes.root}>
      <Header />
      <section>
        <div className={classes.container}>
          <Grid container spacing={1}>
            <div className={classes.centerHeaderView}>
              <ErrorIcon className={classes.emptyHeaderIcon} />
              <b className={classes.emptyHeaderTitle}>
                Essa conta no possui o servio de tickets
              </b>
            </div>
          </Grid>
        </div>
      </section>
    </div>
  );
};

Tickets.propTypes = {
  classes: PropTypes.object,
  alert: PropTypes.object,
  setAlert: PropTypes.func,
  assigneeId: PropTypes.string,
  setAssigneeId: PropTypes.func,
  categoryId: PropTypes.string,
  setCategoryId: PropTypes.func,
  buildingId: PropTypes.string,
  setBuildingId: PropTypes.func,
  code: PropTypes.string,
  setCode: PropTypes.func,
  moveTicketFetch: PropTypes.func,
  ticketToOpenModal: PropTypes.object,
  setTicketToOpenModal: PropTypes.func,
  ticketByCodeFetch: PropTypes.func,
  ticketByCodeLoading: PropTypes.bool,
  ticketsError: PropTypes.object,
  ticketsFetch: PropTypes.func,
  ticketsData: PropTypes.object,
  ticketsLoading: PropTypes.bool,
  ticketLoading: PropTypes.bool,
  ticketCategoriesData: PropTypes.object,
  ticketCategoriesError: PropTypes.object,
  ticketCategoriesLoading: PropTypes.bool,
  ticketAssigneesData: PropTypes.object,
  ticketAssigneesError: PropTypes.object,
  ticketAssigneesLoading: PropTypes.bool
};

const withTicketCategories = withRequest(TicketCategoriesService.getCategories, {
  name: 'ticketCategories'
});

const withTicketAssignees = withRequest(TicketsService.getAssignables, {
  name: 'ticketAssignees'
});

const withMoveTicket = withRequest(TicketsService.moveTicket, {
  name: 'moveTicket',
  init: false
});

const withTicketByCode = withRequest(TicketsService.getTicketByCode, {
  name: 'ticketByCode',
  init: false,
  options: ({ setAlert, setTicketToOpenModal, code }) => ({
    variables: code,
    onRequestFinished: (data) => {
      const ticketByCode = _get(data, 'ticket');
      setTicketToOpenModal(ticketByCode);
      window.history.pushState('', '', `/#/tickets/${_get(ticketByCode, 'id', '')}`);
    },
    onRequestError: () => {
      setTicketToOpenModal(null);
      setAlert({
        alertMsg: 'Cdigo de ticket no identificado',
        alertType: 'warning',
        showAlert: true
      });
      window.history.pushState('', '', '/#/tickets');
    }
  })
});

const withTicket = withRequest(TicketsService.getTicket, {
  name: 'ticket',
  init: false,
  options: ({ match, setAlert, setTicketToOpenModal }) => ({
    variables: _get(match, 'params.id'),
    onRequestFinished: (data) => {
      const ticketById = _get(data, 'ticket');
      setTicketToOpenModal(ticketById);
      window.history.pushState('', '', `/#/tickets/${_get(ticketById, 'id', '')}`);
    },
    onRequestError: () => {
      setTicketToOpenModal(null);
      setAlert({
        alertMsg: 'Ticket no identificado',
        alertType: 'warning',
        showAlert: true
      });
      window.history.pushState('', '', '/#/tickets');
    }
  })
});

const withTickets = withRequest(TicketsService.getTickets, {
  name: 'tickets',
  init: false,
  options: ({ buildingId, assigneeId, categoryId, setTicketToOpenModal }) => ({
    variables: { buildingId, assigneeId, categoryId },
    onRequestFinished: () => {
      setTicketToOpenModal(null);
      window.history.pushState('', '', '/#/tickets');
    }
  })
});

export default compose(
  withStyles(styles, { withTheme: true }),
  withRouter,
  withState('assigneeId', 'setAssigneeId'),
  withState('code', 'setCode', ''),
  withState('ticketToOpenModal', 'setTicketToOpenModal'),
  withState('categoryId', 'setCategoryId'),
  withState('buildingId', 'setBuildingId'),
  withState('alert', 'setAlert', {
    alertMsg: '',
    alertType: '',
    showAlert: false
  }),
  withTickets,
  withTicket,
  withTicketByCode,
  withTicketCategories,
  withTicketAssignees,
  withMoveTicket,
  lifecycle({
    componentDidMount() {
      const {
        ticketsFetch, ticketFetch, setAssigneeId,
        setBuildingId, ticketToOpenModal, match
      } = this.props;
      const user = Session.getUserData();
      const buildings = _sortBy(_get(user, 'buildings', []), ['name']);
      const initBuildingId = _get(buildings, '[0].id', '');
      const currentRole = _get(user, 'member.role', MEMBER_ROLE);
      const initAssigneeId = currentRole !== MEMBER_ROLE ? _get(user, 'member_id') : '';
      setAssigneeId(initAssigneeId);
      setBuildingId(initBuildingId);
      const paramsId = _get(match, 'params.id');
      if (_isEmpty(ticketToOpenModal)) {
        ticketsFetch(true);
      }
      if (_size(paramsId)) {
        ticketFetch(false);
      }
    }
  })
)(Tickets);
