import Api from '../utils/api';

const Accounts = {
  verifyIfCanResetPassword(memberId, recoveryCode) {
    const body = { id: memberId, code: recoveryCode };
    return Api.post('/password/verify', body);
  },
  changePassword(memberId, recoveryCode, password) {
    const body = { id: memberId, code: recoveryCode, password };
    return Api.put('/password/change', body);
  },
  resetPassword(email) {
    const body = { email };
    return Api.post('/password/reset', body);
  }
};

export default Accounts;
