import React, { memo } from 'react';
import PropTypes from 'prop-types';
import _map from 'lodash/map';
import _get from 'lodash/get';
import { Droppable, Draggable } from 'react-beautiful-dnd';
import styles from '../styles';
import Card from '../Card';

const ListContainer = memo(({ provided = {}, style, children }) => (
  <div style={{ ...styles.list, ...style }} ref={provided.innerRef} {...provided.droppableProps}>
    {children}
  </div>
));

ListContainer.propTypes = {
  children: PropTypes.any,
  provided: PropTypes.any,
  style: PropTypes.any
};

const InnerList = memo(({
  column, cardWrapperStyle, renderCard, disableDragDrop
}) => (
  _map(_get(column, 'rows', []), (row, index) => {
    if (disableDragDrop) {
      return (
        <Card
          key={index}
          row={row}
          renderCard={renderCard}
          cardWrapperStyle={cardWrapperStyle}
          disableDragDrop={disableDragDrop}
        />
      );
    }
    return (
      <Draggable key={row.id} draggableId={row.id} index={index}>
        {(dragProvided, dragSnapshot) => (
          <Card
            key={index}
            row={row}
            provided={dragProvided}
            dragSnapshot={dragSnapshot}
            renderCard={renderCard}
            cardWrapperStyle={cardWrapperStyle}
            disableDragDrop={disableDragDrop}
          />
        )}
      </Draggable>
    );
  })
));

InnerList.propTypes = {
  column: PropTypes.object,
  cardWrapperStyle: PropTypes.object,
  renderCard: PropTypes.func,
  disableDragDrop: PropTypes.bool
};

const List = memo(({
  listStatus, column, disableDragDrop, loading,
  renderCard, columnStyle, cardWrapperStyle
}) => (
  disableDragDrop ?
    <ListContainer style={columnStyle}>
      <InnerList
        column={column}
        renderCard={renderCard}
        disableDragDrop={disableDragDrop}
        cardWrapperStyle={cardWrapperStyle}
      />
    </ListContainer>
    :
    <Droppable
      droppableId={listStatus || 'ROW'}
      isDropDisabled={false}
      type="ROW"
    >
      {(dropProvided) => (
        <ListContainer style={columnStyle} provided={dropProvided}>
          <InnerList
            column={column}
            renderCard={renderCard}
            disableDragDrop={disableDragDrop}
            cardWrapperStyle={cardWrapperStyle}
          />
          {dropProvided.placeholder}
        </ListContainer>
      )}
    </Droppable>
));

List.propTypes = {
  listStatus: PropTypes.string,
  renderCard: PropTypes.func,
  loading: PropTypes.bool,
  column: PropTypes.object,
  columnStyle: PropTypes.object,
  cardWrapperStyle: PropTypes.object,
  disableDragDrop: PropTypes.bool
};

export default List;
