import React, { memo } from 'react';
import _get from 'lodash/get';
import _sortBy from 'lodash/sortBy';
import _mapValues from 'lodash/mapValues';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router';
import { compose, withState, lifecycle } from 'recompose';
import Header from '../../components/Header';
import AlertBox from '../../components/AlertBox';
import SettingsMenu from '../../components/SettingsMenu';
import withRequest from '../../utils/with-request';
import Member from '../../services/member';
import EnterpriseUserService from '../../services/enterprise-user';
import Session from '../../utils/session';
import styles from './styles';

const MyProfile = memo(({
  alert, setAlert, classes,
  updateMemberFetch, updateMemberError, updateMemberLoading,
  email, name, lastname, setName, setLastname
}) => {
  const { showAlert, alertMsg, alertType } = alert;
  const onClearAlert = () => setAlert({ alertMsg: '', alertType: '', showAlert: false });
  const onSubmit = () => updateMemberFetch(true);
  const errors = _mapValues(_get(updateMemberError, 'errors', {}), (err) => err[0]);
  let alertBox = null;
  if (showAlert) {
    alertBox = <AlertBox message={alertMsg} type={alertType} onClose={onClearAlert} />;
  }
  const isDisabled = updateMemberLoading;
  return (
    <div className={classes.root}>
      <Header alertBox={alertBox} />
      <section>
        <div className={classes.container}>
          <div className={classes.headerView}>
            <aside>
              <Typography variant="h5">
                <b>Configuraes</b>
              </Typography>
            </aside>
          </div>
        </div>
        <div className={classes.container}>
          <SettingsMenu />
        </div>
        <br />
        <div className={classes.container}>
          <Paper className={classes.paper}>
            <Grid container item xs={12} sm={6} md={4}>
              <Grid item xs={12}>
                <FormControl fullWidth error={!!errors.name}>
                  <TextField
                    label="Nome"
                    margin="normal"
                    variant="outlined"
                    className={classes.input}
                    InputLabelProps={{ shrink: true }}
                    disabled={isDisabled}
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                  />
                  {errors.name && <FormHelperText>{errors.name}</FormHelperText>}
                </FormControl>
              </Grid>
              <Grid item xs={12}>
                <FormControl fullWidth error={!!errors.lastname}>
                  <TextField
                    label="Sobrenome"
                    margin="normal"
                    variant="outlined"
                    className={classes.input}
                    InputLabelProps={{ shrink: true }}
                    disabled={isDisabled}
                    value={lastname}
                    onChange={(e) => setLastname(e.target.value)}
                  />
                  {errors.lastname && <FormHelperText>{errors.lastname}</FormHelperText>}
                </FormControl>
              </Grid>
              <Grid item xs={12}>
                <FormControl fullWidth>
                  <TextField
                    disabled
                    label="Email"
                    margin="normal"
                    variant="outlined"
                    className={classes.input}
                    InputLabelProps={{ shrink: true }}
                    value={email}
                  />
                </FormControl>
              </Grid>
              <Grid item xs={12}>
                <div className={classes.actions}>
                  <Button
                    onClick={onSubmit}
                    size="large"
                    color="primary"
                    variant="contained"
                    disabled={isDisabled}
                  >
                    Atualizar
                  </Button>
                </div>
              </Grid>
            </Grid>
          </Paper>
        </div>
      </section>
    </div>
  );
});

MyProfile.propTypes = {
  classes: PropTypes.object,
  name: PropTypes.string,
  email: PropTypes.string,
  setName: PropTypes.func,
  lastname: PropTypes.string,
  setLastname: PropTypes.func,
  updateMemberLoading: PropTypes.bool,
  updateMemberFetch: PropTypes.func,
  updateMemberError: PropTypes.func,
  alert: PropTypes.object,
  setAlert: PropTypes.func
};

const withUpdateMember = withRequest(Member.updateMember, {
  name: 'updateMember',
  init: false,
  options: ({ name, lastname, setAlert, setName, setLastname }) => ({
    variables: { name, lastname },
    onRequestFinished: async () => {
      try {
        const enterpriseUserResponse = await EnterpriseUserService.getEnterpriseUser();
        const enterpriseUser = _get(enterpriseUserResponse, 'enterprise_user');
        Session.setUserData({
          ...enterpriseUser,
          ...{ buildings: _sortBy(_get(enterpriseUser, 'buildings'), ['name']) }
        });
        setName(name);
        setLastname(lastname);
        setAlert({ alertMsg: 'Perfil atualizado com sucesso', alertType: 'success', showAlert: true });
      } catch {
        setAlert({ alertMsg: 'Erro ao atualizar perfil', alertType: 'error', showAlert: true });
      }
    },
    onRequestError: () => {
      setAlert({ alertMsg: 'Erro ao atualizar perfil', alertType: 'error', showAlert: true });
    }
  })
});

export default compose(
  withStyles(styles, { withTheme: true }),
  withRouter,
  withState('alert', 'setAlert', {
    alertMsg: '',
    alertType: '',
    showAlert: false
  }),
  withState('disabled', 'setDisabled', false),
  withState('name', 'setName', _get(Session.getUserData(), 'member.name')),
  withState('lastname', 'setLastname', _get(Session.getUserData(), 'member.lastname')),
  withState('email', 'setEmail', _get(Session.getUserData(), 'member.email')),
  withUpdateMember,
  lifecycle({
    componentDidMount() {
      const { setName, setLastname, setEmail } = this.props;
      const userData = Session.getUserData();
      setName(_get(userData, 'member.name'));
      setLastname(_get(userData, 'member.lastname'));
      setEmail(_get(userData, 'member.email'));
    }
  })
)(MyProfile);
