import React, { memo } from 'react';
import PropTypes from 'prop-types';
import _size from 'lodash/size';
import { withStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Loading from '../../../components/Loading';
import VisitorsItemRow from '../VisitorsItemRow';
import styles from './styles';

const VisitorsTable = memo(({ classes, visitors, loading, onCancelVisitor }) => (
  <div className={classes.container}>
    <Paper className={classes.paper}>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell className={classes.tableCell}>DATA VISITA</TableCell>
            <TableCell className={classes.tableCell}>STATUS</TableCell>
            <TableCell className={classes.tableCell}>VISITANTE</TableCell>
            <TableCell className={classes.tableCell}>VISITA PARA</TableCell>
            <TableCell className={classes.tableCell}>CRIADO POR</TableCell>
            <TableCell className={classes.tableCell}>AO</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {
            loading ?
              <TableRow>
                <TableCell colSpan={6} align="center">
                  <Loading />
                </TableCell>
              </TableRow>
              :
              (
                _size(visitors) ?
                  (visitors.map((visitor, i) => {
                    return (
                      <VisitorsItemRow
                        index={i}
                        key={visitor.id}
                        visitor={visitor}
                        loading={loading}
                        onCancelVisitor={onCancelVisitor}
                      />
                    );
                  }))
                  :
                  <TableRow>
                    <TableCell className={classes.empty} colSpan={6} align="center">
                      Nenhum visitante registrado
                    </TableCell>
                  </TableRow>
              )
          }
        </TableBody>
      </Table>
    </Paper>
  </div>
));

VisitorsTable.propTypes = {
  visitors: PropTypes.array,
  loading: PropTypes.bool,
  classes: PropTypes.object,
  onCancelVisitor: PropTypes.func
};

export default withStyles(styles, { withTheme: true })(VisitorsTable);
