import Api from '../utils/api';

const Members = {
  getMembers() {
    return Api.get('/members');
  },
  getMember(id) {
    return Api.get(`/members/${id}`);
  },
  updateBuildingRole({ memberId, buildingId, role }) {
    return Api.put(
      '/enterprise_users/update_role',
      { member_id: memberId, building_id: buildingId, role }
    );
  },
  blockMember(id) {
    return Api.put(`/members/${id}/block`);
  },
  updateMember({ id, name, lastname, phone, role, buildingIds }) {
    return Api.put(
      `/members/${id}`,
      { id, name, lastname, phone, role, building_ids: buildingIds }
    );
  }
};

export default Members;
